import React, { Suspense, lazy } from 'react';
import ErrorBoundary from '../ErrorBoundary';
import Loading from '../Loading';

const initalOption = {
  delay: 200,
  timeout: 1000 * 30,
};

let timer = null;

// 动态加载
const LazyLoad = (loader, options = initalOption) => {
  const { delay, timeout } = options;
  const clearTime = () => {
    clearTimeout(timer);
    timer = null;
  };

  const LazyComponent = lazy(() => {
    return Promise.race([
      // 加载超时
      new Promise((resolve, reject) => {
        timer = setTimeout(() => {
          clearTime();
          reject('timeout');
        }, timeout);
      }),
      // 组件加载
      new Promise((resolve) => {
        timer = setTimeout(() => {
          clearTime();
          resolve(loader);
        }, delay);
      }),
    ]).then(
      (loader) => loader(),
      (errInfo) => Promise.reject(new Error(errInfo)),
    );
  });

  return (
    <ErrorBoundary>
      <Suspense fallback={<Loading />}>
        <LazyComponent />
      </Suspense>
    </ErrorBoundary>
  );
};

export default LazyLoad;
