___CSS_LOADER_EXPORT___.push([module.id, `.block {
  position: absolute;
  background-color: #0c9eff;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
  cursor: default;
  width: 110px;
  top: 0%;
  left: 0%;
  aspect-ratio: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 30px;
  font-weight: bold;
  letter-spacing: 1px;
  padding: 2px 8px;
  border-radius: 4px;
  z-index: 100;
}
.block::after {
  font-family: DS-DIGI;
  content: 'world';
  display: block;
}
.grab {
  cursor: grab;
}
.grabbing {
  cursor: grabbing;
}
.move {
  position: absolute;
  background-color: #880000;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
  width: 200px;
  aspect-ratio: 6;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: hidden;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.move:hover {
  animation-play-state: paused;
  cursor: pointer;
}
.move .rotation {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0%, -50%);
  display: inline-block;
  white-space: nowrap;
  text-align: left;
  color: #fff;
}
@keyframes loop {
  0% {
    left: 0%;
    transform: translate(0%, -50%);
  }
  100% {
    left: 100%;
    transform: translate(-100%, -50%);
  }
}
