import React, { useEffect, useRef } from 'react';
import './style';
import ToChineseNum from './components/ToChineseNum';
import Monitor from './components/Monitor';
import Lazyload from './components/Lazyload';
import MouseMove from './components/MouseMove';

const content = `1:在一个小锅中，将水、黄油和盐煮沸。
2:一旦水沸腾，降低火力，加入面粉，迅速搅拌直到混合物形成一个球状的面团。
3:将面团转移到一个大碗中，让它稍微冷却一会儿。
4:逐个加入鸡蛋，搅拌至光滑。确保每个鸡蛋充分融合到面团中后再加下一个。
5:预热烤箱至 375°F（190°C）。
6:将面团挤成小球，放在烤盘上，留有一定间距。
7:在烤箱中烘烤约25-30分钟，直到泡芙变得金黄色和蓬松。
8:烤好后，用锯齿刀在一侧切开泡芙，然后填充馅料。
`;

const textSkill = () => {
  const textRef = useRef();

  // 自动添加文本
  const autoAppend = async () => {
    for (const key in content) {
      const text = content.slice(0, key);
      const result = transform(text);
      await delay(100);
      textRef.current.innerHTML = null;
      if (result) {
        textRef?.current?.append(result);
        updateCursor();
      }
    }
  };

  const delay = (time = 1000) => {
    return new Promise(resolve => {
      setTimeout(() => resolve(), time);
    });
  };

  const transform = text => {
    if (!text) return;
    //  根据传进来的文本节点来判断有几个换行符
    const domFragment = document.createDocumentFragment();
    text.split('\n').forEach(textInfo => {
      const dom = document.createElement('p');
      dom.innerHTML = textInfo;
      domFragment.append(dom);
    });
    return domFragment;
  };

  const getLastTextNode = node => {
    if (node.nodeType === Node.TEXT_NODE) return node;
    const children = node?.childNodes;
    for (let i = children?.length - 1; i >= 0; i--) {
      let result = getLastTextNode(children[i]);
      if (result) return result;
    }
    return null; // 空节点
  };
  // 更新光标位置
  const updateCursor = () => {
    const textWrap = document.querySelector('.textContainer');
    // 设置光标卫位置
    const cursor = document.querySelector('.cursor');
    // 找到最后一个文本节点
    const lastTextNode = getLastTextNode(textWrap);
    // 加文字
    const tempText = document.createTextNode('lee');
    lastTextNode
      ? lastTextNode.parentNode.append(tempText)
      : textWrap.append(tempText);
    // 根据文本位置设置光标位置
    const range = document.createRange(); // 获取选中的文本的坐标
    range.setStart(tempText, 0);
    range.setEnd(tempText, 0);
    const rect = range.getBoundingClientRect();
    const wrapRect = textWrap.getBoundingClientRect();

    const left = rect.left - wrapRect.left;
    const top = rect.top - wrapRect.top - wrapRect.height; // 保持与文本框的高度一致
    cursor.style.transform = `translate(${left}px,${top}px)`;
    // 删文字
    tempText.remove();
  };
  useEffect(() => {
    if (textRef && textRef?.current) autoAppend();
  }, [textRef]);
  return (
    <div className="textSkill">
      {/* 鼠标移动效果 */}
      {/* <MouseMove /> */}
      {/* 数字转中文大写 */}
      <ToChineseNum />
      {/* 监听尺寸变化 */}
      {/* <Monitor /> */}
      <Lazyload />
      {/* 光点打印 */}
      {/* <>
        <div className="textContainer" ref={textRef} />
        <div className="cursor" />
      </> */}
    </div>
  );
};

export default textSkill;
