class Promise {
  status = 'pending';
  value = null;
  reason = null;
  onFullfilled = [];
  onRejected = [];

  constructor(executor) {
    const resolve = data => {
      if (this.status !== 'pending') return;
      this.status = 'fullfilled';
      this.value = data;
      if (!this.onFullfilled.length) return;
      this.onFullfilled.forEach(fn => fn());
    };
    const reject = reason => {
      if (this.status !== 'pending') return;
      this.reason = reason;
      this.status = 'rejected';
      if (!this.onRejected.length) return;
      this.onRejected.forEach(fn => fn());
    };
    try {
      executor(resolve, reject);
    } catch (error) {
      reject(error);
    }
  }

  then(resolveCallbak, rejectCallback) {
    resolveCallbak =
      resolveCallbak && typeof resolveCallbak === 'function'
        ? resolveCallbak
        : val => val;
    rejectCallback =
      rejectCallback && typeof rejectCallback === 'function'
        ? rejectCallback
        : reason => {
            throw new Error(reason);
          };
    return new Promise((resolve, reject) => {
      // pending => fullfilled
      const doFullfilled = () => {
        try {
          const result = resolveCallbak(this.value);
          return result instanceof Promise
            ? result.then(resolve, reject)
            : resolve(result);
        } catch (error) {
          reject(error);
        }
      };

      // pending => rejected
      const doRejected = () => {
        try {
          const result = rejectCallback(this.reason);
          return result instanceof Promise
            ? result.then(resolve, reject)
            : reject(result);
        } catch (error) {
          reject(error);
        }
      };

      if (this.status === 'fullfilled') doFullfilled();
      if (this.status === 'rejected') doRejected();
      if (this.status === 'pending') {
        this.onFullfilled.push(doFullfilled);
        this.onRejected.push(doRejected);
      }
    });
  }

  catch(rejectCallback) {
    return this.then(null, rejectCallback);
  }

  /**
   * finally 处理程序将结果或 error “传递”给下一个合适的处理程序
   * 其中value 或者 reason就是上一个程序的返回结果，在finally方法里面什么也不会被做。
   * finally函数根据放置的位置，接收上一个程序的处理值进行传递。
   * @param {*} callback
   * @returns
   */
  finally(callback) {
    return this.then(
      value =>
        Promise.resolve(callback()).then(() => {
          // console.log('value是上一个操作程序,resolve返回结果：' + value);
          return value;
        }),
      reason =>
        Promise.resolve(callback()).then(() => {
          // console.log('reason是上一个操作程序,rejecte返回结果：' + reason);
          throw reason;
        })
    );
  }

  static resolve(result) {
    if (result instanceof Promise) return result;
    return new Promise(resolve => resolve(result));
  }

  static reject(reason) {
    return new Promise((resolve, reject) => reject(reason));
  }

  static all(promiseArr = []) {
    return new Promise((resolve, reject) => {
      if (!promiseArr?.length) resolve();
      const result = [];
      promiseArr.forEach(promise => {
        promise.then(
          val => {
            result.push(val);
            result.length === promiseArr.length && resolve(result);
          },
          err => reject(err)
        );
      });
    });
  }

  static race(promiseArr) {
    return new Promise((resolve, reject) => {
      if (!promiseArr.length) resolve();
      promiseArr.forEach(promise => {
        promise.then(
          result => resolve(result),
          error => reject(error)
        );
      });
    });
  }

  /**
   * 获取promise队列返回第一个resolve的值
   * @param {*} promiseArr
   * @returns
   */
  static any(promiseArr) {
    return new Promise((resolve, reject) => {
      if (!promiseArr.length) resolve();
      let result = [];
      promiseArr.forEach(promise => {
        promise.then(
          result => {
            resolve(result);
          },
          error => {
            result.push(error);
            result.length === promiseArr.length && reject(result);
          }
        );
      });
    });
  }
}

export default Promise;

