  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}
.visualization .canvasDom {
  width: 100%;
  height: 100%;
}
.visualization .tooltips {
  position: absolute;
  transition: all 0.2s linear;
  height: 7rem;
  background: #057ada;
  border-radius: 0.2rem;
  z-index: 10;
  top: 0px;
  left: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.2rem;
  padding-right: 1.4rem;
  visibility: hidden;
}
.visualization .tooltips div {
  display: flex;
  width: 100%;
  color: #fff;
}
.visualization .tooltips div > span {
  display: inline-block;
  padding: 0.2rem 0;
}
.visualization .tooltips div > span:nth-child(1) {
  width: 6rem;
  text-align: right;
}
