___CSS_LOADER_EXPORT___.push([module.id, `.viewport {
  width: 60%;
  height: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: hidden;
  font-family: 'DS-DIGI';
  transition: transform 0.3s linear;
}
.viewport .slider {
  position: relative;
  height: 100%;
  display: flex;
  transition: transform 0.3s linear;
  cursor: default;
}
.viewport .slider .content {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 6rem;
  color: #fff;
  flex: 0 0 100%;
}
.viewport .pointer {
  position: absolute;
  bottom: 0.5rem;
  left: 50%;
  display: flex;
  align-items: center;
  transform: translate(-50%, 0);
  opacity: 0;
  transition: all 0.4s linear;
  z-index: 1;
}
.viewport .pointer .point {
  display: inline-block;
  width: 0.7rem;
  height: 0.7rem;
  margin: 0rem 0.2rem;
  border-radius: 50%;
  cursor: pointer;
  box-sizing: border-box;
  transition: all 0.3s linear;
  border: 0.05rem solid #fff;
}
.viewport .pointer .active {
  background: red;
  border: 0.05rem solid #fff;
}
.viewport .LPointer,
.viewport .RPointer {
  position: absolute;
  top: 50%;
  font-size: 6rem;
  transform: translate(0%, -50%);
  z-index: 1;
  opacity: 0;
  cursor: pointer;
  color: #fff;
  transition: all 0.4s linear;
}
.viewport .LPointer {
  left: 2%;
}
.viewport .RPointer {
  right: 2%;
}
.viewport:hover .LPointer,
.viewport:hover .RPointer,
.viewport:hover .pointer {
  opacity: 1;
}
