/***
 * 实现一个div块在父级容器内拖拽
 */
import { clientToScreen, screenToClient } from '../../utils/common';
import './style.less';

// 通信机制
const channel = new BroadcastChannel('card');

channel.onmessage = (e) => {
  const [x, y] = screenToClient(...e.data);
  const dom = document.querySelector('.card');
  dom.style.left = x + 'px';
  dom.style.top = y + 'px';
};
/**
 * 拖拽组件
 * @param {*} dom 被拖拽的组件
 * @param {*} IsBroadcast 是否拖动页面间通信
 * @returns
 */
const DragBlock = function (dom, IsBroadcast = false) {
  // 获取父级容器信息
  // const parentNode = document.querySelector('.container');
  if (!dom) return;
  const parentNode = dom.parentNode;
  const poffsetX = parentNode.offsetLeft;
  const poffsetY = parentNode.offsetTop;
  const parentNodeWidth = parentNode.getBoundingClientRect().width;
  const parentNodeHeight = parentNode.getBoundingClientRect().height;

  // 创建移动块
  let startX, startY;
  // const dom = document.createElement('div');`
  dom.classList.add('dom');
  // parentNode.append(dom);

  dom.onmousedown = function (e) {
    dom.classList.add('grab');
    const { clientX, clientY } = e;
    startX = clientX - dom.offsetLeft - poffsetX;
    startY = clientY - dom.offsetTop - poffsetY;
    const blockWidth = dom.getBoundingClientRect().width;
    const blockHeight = dom.getBoundingClientRect().height;
    // 移动
    document.onmousemove = function (event) {
      dom.classList.add('grabbing');
      // 计算位移
      let mleft = event.clientX - startX - parentNode.offsetLeft;
      let mtop = event.clientY - startY - parentNode.offsetTop;

      const setPosition = () => {
        //  blockWidth / 2 是因为 transform: translate(-50%, -50%); 垂直居中，偏移了一半。
        dom.style.left = mleft + 'px';
        dom.style.top = mtop + 'px';
        // 边界值处理
        if (mleft <= 0) dom.style.left = 0 + 'px';
        if (mleft >= parentNodeWidth - blockWidth)
          dom.style.left = parentNodeWidth - blockWidth + 'px';

        if (mtop <= 0) dom.style.top = 0 + 'px';
        if (mtop >= parentNodeHeight - blockHeight)
          dom.style.top = parentNodeHeight - blockHeight + 'px';

        // 页面间是否通信
        if (IsBroadcast) {
          const { top, left } = dom.getBoundingClientRect();
          const points = clientToScreen(left, top);
          channel.postMessage(points);
        }

        window.requestAnimationFrame(setPosition);
      };

      window.requestAnimationFrame(setPosition);
    };

    // 结束
    document.onmouseup = function () {
      document.onmousemove = null;
      document.onmouseup = null;
      dom.classList.remove('grabbing');
      dom.classList.remove('grab');
    };

    return false;
  };
};

export default DragBlock;
