import {
  BufferGeometry,
  ExtrudeGeometry,
  Line,
  Mesh,
  MeshBasicMaterial,
  MeshPhongMaterial,
  Object3D,
  QuadraticBezierCurve3,
  Shape,
  Vector3,
} from 'three';
import chinaJson from '@/static/json/china.json';
import { randomHexColor } from '@/utils/common';
import * as d3 from 'd3';
import { spotCircle } from '../Point';

const CNMap = new Object3D();

const projection = d3
  .geoMercator()
  .center([116.23128, 40.22077])
  .translate([0, 0]);

const depth = 4;
chinaJson.features.forEach((Feature) => {
  const province = new Object3D();
  const { geometry, properties } = Feature;
  province.properties = properties;

  geometry.coordinates.forEach((multiPolygon) => {
    multiPolygon.forEach((poloy) => {
      const shape = new Shape();
      const points = [];
      for (let i = 0; i < poloy.length; i++) {
        const [x, y] = projection(poloy[i]);
        if (i === 0) {
          shape.moveTo(x, -y);
        }
        shape.lineTo(x, -y);
        points.push(new Vector3(x, -y, depth));
      }
      // 绘制省份轮廓
      const lineMaterial = new MeshBasicMaterial({ color: randomHexColor() });
      const lineGeometry = new BufferGeometry();
      lineGeometry.setFromPoints(points);
      const line = new Line(lineGeometry, lineMaterial);
      province.add(line);
      // 绘制省份
      const provincGeometry = new ExtrudeGeometry(shape, { depth });
      const panelMaterial = new MeshPhongMaterial({
        color: '#00ccff',
        transparent: true,
        opacity: 0.4,
      });
      const plineMaterial = new MeshPhongMaterial({
        color: '#00ccff',
        transparent: true,
        opacity: 1,
      });
      const provideMesh = new Mesh(provincGeometry, [
        panelMaterial,
        plineMaterial,
      ]);
      province.add(provideMesh);
    });
  });
  CNMap.add(province);
});

export { CNMap, projection };
