import Promise from '../Promise';

const Jsonp = function (options) {
  const { url = '', data = null } = options;
  return new Promise((resolve, reject) => {
    const callback = 'callback';

    const jsonp = document.createElement('script');
    jsonp.type = 'text/javascript';
    jsonp.id = 'jsonp';

    let params = `${url}?callback=${callback}`;
    for (let key in data) params += `&${key}=${data[key]}`;
    // Object.keys(data).forEach((key) => {
    //   params += `&${key}=${data[key]}`;
    // });
    jsonp.src = params;

    // 处理函数
    window[callback] = function (result) {
      removeJsonp();
      resolve(result);
    };
    jsonp.onerror = function (error) {
      removeJsonp();
      reject(error);
    };

    // 添加dom
    document.head.append(jsonp);
    // 移除dom
    const removeJsonp = () => {
      document.getElementById('jsonp')?.remove();
      delete window[callback];
    };
  });
};

export default Jsonp;
