export const throttle = (fn, delay = 1000) => {
  let timer = null;
  return function () {
    if (timer) return;
    timer = setTimeout(() => {
      fn.call(null, ...arguments);
      clearTimeout(timer);
      timer = null;
    }, delay);
  };
};

//  去除对象中的空值和未定义值
export const GetUsefulParams = (formParams) => {
  const filterParams = {};
  Object.keys(formParams).length &&
    Object.keys(formParams).forEach((key) => {
      const value = formParams[key];
      if (value !== '' && value !== undefined && value !== null) {
        if (typeof value === 'string') {
          if (value.trim() !== '') {
            filterParams[key] = value.trim();
          }
        } else if (isArray(value)) {
          const newVal = value
            .map((item) => {
              if (isString(item)) {
                return item.trim();
              }
              return item;
            })
            .filter((item) => item !== '');
          filterParams[key] = newVal;
        } else {
          filterParams[key] = value;
        }
      }
    });
  return filterParams;
};

/**
 * 设置Rem
 * @param {*} container 真实Dom节点
 */
export const setRem = (container) => {
  const clientWidth = container.clientWidth;
  // const fontSize = (100 * 20) / clientWidth;
  const fontSize = (100 * 20) / 1920;
  document.documentElement.style.fontSize = `${fontSize}vw`;
};

import { isNumber, isFinite } from 'lodash';

const kUnit = 1000;

export function unhumansize(num) {
  if (num === null || num === undefined) {
    return num;
  }
  if (!num) {
    return `${num}`;
  }
  if (isNumber(+num) && isFinite(+num)) {
    return parseInt(num, 10);
  }
  if (typeof num !== 'string') {
    return num;
  }
  if (num.match(/\d+(\.\d+)?[KMGTPEZYkmgtpezy]$/)) {
    const unitVal = {
      k: kUnit,
      m: kUnit ** 2,
      g: kUnit ** 3,
      t: kUnit ** 4,
      p: kUnit ** 5,
      e: kUnit ** 6,
      z: kUnit ** 7,
      y: kUnit ** 8,
    };
    const unit = num.match(/[KMGTPEZYkmgtpezy]$/)[0].toLowerCase();
    const result = parseFloat(num) * unitVal[unit];
    // 输入11111111p后使用parseInt由于数值过大超出最大值，返回1异常，使用Math.floor()代替
    //return parseInt(result, 10);
    return Math.floor(result);
  }
  if (num === '999+') {
    return kUnit ** 6;
  }
  return num;
}

export function humansize(num, humansizePrecision = 10) {
  const mUnit = kUnit ** 2;
  const gUnit = kUnit ** 3;
  const tUnit = kUnit ** 4;
  const pUnit = kUnit ** 5;
  const eUnit = kUnit ** 6;
  const zUnit = kUnit ** 7;
  const yUnit = kUnit ** 8;
  const lUnit = kUnit ** 9;

  if (typeof num === 'undefined' || num === null) {
    return '--';
  }
  if (!num || !isNumber(num)) {
    return `${num}`;
  }
  let result = parseInt(num, 10);
  let val = '';
  if (num >= kUnit && num < mUnit) {
    val = Math.round((num / kUnit) * humansizePrecision) / humansizePrecision;
    result = `${val}K`;
  } else if (num >= mUnit && num < gUnit) {
    val = Math.round((num / mUnit) * humansizePrecision) / humansizePrecision;
    result = `${val}M`;
  } else if (num >= gUnit && num < tUnit) {
    val = Math.round((num / gUnit) * humansizePrecision) / humansizePrecision;
    result = `${val}G`;
  } else if (num >= tUnit && num < pUnit) {
    val = Math.round((num / tUnit) * humansizePrecision) / humansizePrecision;
    result = `${val}T`;
  } else if (num >= pUnit && num < eUnit) {
    val = Math.round((num / pUnit) * humansizePrecision) / humansizePrecision;
    result = `${val}P`;
  } else if (!Number.isSafeInteger(parseInt(num, 10))) {
    val = '999+';
    result = `${val}P`;
    return result;
  } else if (num >= eUnit && num < zUnit) {
    val = Math.round((num / eUnit) * humansizePrecision) / humansizePrecision;
    result = `${val}E`;
  } else if (num >= zUnit && num < yUnit) {
    val = Math.round((num / zUnit) * humansizePrecision) / humansizePrecision;
    result = `${val}Z`;
  } else if (num >= yUnit) {
    val = Math.round((num / yUnit) * humansizePrecision) / humansizePrecision;
    result = `${val}Y`;
  } else if (num >= lUnit) {
    val = '999+';
    result = `${val}Y`;
  } else {
    result = Math.round(num * humansizePrecision) / humansizePrecision;
  }
  return result;
}

export function unhumansizeFlowQueryParams(paramsArr = []) {
  if (paramsArr.constructor != Array || paramsArr.length <= 0) {
    return undefined;
  }
  let resParamsArr = [];
  const paramsArrLen = paramsArr.length;
  for (let i = 0; i < paramsArrLen; i++) {
    const paramsItem = paramsArr[i];
    if (paramsItem.indexOf('-') > -1) {
      let paramsItemArrTmp = [];
      const paramsItemArr = paramsItem.split('-');
      const paramsItemArrLen = paramsItemArr.length;
      for (let j = 0; j < paramsItemArrLen; j++) {
        paramsItemArrTmp.push(unhumansize(paramsItemArr[j]));
      }
      resParamsArr.push(paramsItemArrTmp.join('-'));
    } else {
      const paramsItemNum = unhumansize(paramsItem);
      resParamsArr.push(paramsItemNum.toString());
    }
  }
  if (resParamsArr.length <= 0) {
    return undefined;
  }
  return resParamsArr;
}

// 判断是不是换行符
export const isLineBreak = (char) => char.charCodeAt(0) === 10;

// 调用IntersectionObserver监听
export class ObserverInstance {
  constructor(callback, options) {
    this.observerInstance = null;
    this.instance = new IntersectionObserver(callback, options);
  }
  // 获取实例信息
  static getInstance(
    callback = () => {},
    options = {
      root: null,
      rootMargin: '0px',
      threshold: 0.2,
    },
  ) {
    if (!this.observerInstance)
      this.observerInstance = new ObserverInstance(callback, options);
    // 返回对象里面的 instance 属性
    return this.observerInstance.instance;
  }
}

// 获取真正的浏览器视口高度
const barHeight = () => window.outerHeight - window.innerHeight;

// 获取屏幕坐标
export const clientToScreen = function (x, y) {
  const screenX = x + window.screenX;
  const screenY = y + window.screenY + barHeight();
  return [screenX, screenY];
};

// 获取屏幕到视口的坐标
export const screenToClient = function (x, y) {
  const clientX = x - window.screenX;
  const clientY = y - window.screenY - barHeight();
  return [clientX, clientY];
};
