import {
  Group,
  Mesh,
  MeshBasicMaterial,
  RingGeometry,
  Spherical,
  Vector3,
} from 'three';
import { YELLOW_RED_ANGLE, hexagonRadius } from '../GlobalConfig';
import { TextGeometry } from 'three/addons/geometries/TextGeometry.js';
/**
 * 绘制夜晚星空效果
 * @param {} ctx
 * @param {*} xdeg
 * @param {*} ydeg
 */
export const drawNightEarthAlphaMap = (ctx, xdeg, ydeg) => {
  if (ctx) {
    const canvasHeight = ctx.canvas.height;
    const canvasWidth = ctx.canvas.width;
    const alphaRadius = canvasHeight / 2;
    // const x0 = canvasWidth * ((xdeg + Math.PI + Math.PI / 12) / (2 * Math.PI));
    const x0 = canvasWidth * ((xdeg + Math.PI - Math.PI / 24) / (2 * Math.PI));
    const y0 = canvasHeight * ((ydeg + YELLOW_RED_ANGLE) / Math.PI);
    const maps = [
      [x0, y0],
      [x0 - canvasWidth, y0],
      [x0 + canvasWidth, y0],
      [x0, y0 - canvasHeight],
      [x0 - canvasWidth, y0 - canvasHeight],
      [x0 + canvasWidth, y0 - canvasHeight],
      [x0, y0 - canvasHeight],
      [x0 - canvasWidth, y0 + canvasHeight],
      [x0 + canvasWidth, y0 + canvasHeight],
    ];
    ctx.save();
    ctx.fillStyle = '#000000';
    ctx.fillRect(0, 0, canvasWidth, canvasHeight);
    ctx.restore();
    maps.forEach((item) => {
      const [x, y] = item;
      ctx.save();
      const gradient = ctx.createRadialGradient(
        x,
        y,
        alphaRadius,
        x,
        y,
        0.7 * alphaRadius,
      );
      gradient.addColorStop(0, '#000');
      gradient.addColorStop(1, '#fff');
      ctx.fillStyle = gradient;
      ctx.fillRect(
        x - alphaRadius,
        y - alphaRadius,
        canvasHeight,
        canvasHeight,
      );
      ctx.restore();
    });
  }
};

/**
 * 将二维经纬度坐标，转换成空间球坐标
 * @param {*} lnglat
 * @param {*} customRadius
 * @param {*} addTheta
 * @param {*} addPhi
 * @returns
 */
export function createPosition(
  lnglat,
  customRadius = 100,
  addTheta = 0,
  addPhi = 0,
) {
  const spherical = new Spherical();
  spherical.radius = customRadius;
  const lng = parseFloat(lnglat[0]);
  const lat = parseFloat(lnglat[1]);
  const theta = (lng + 90) * (Math.PI / 180) + addTheta;
  const phi = (90 - lat) * (Math.PI / 180) + addPhi;
  spherical.phi = phi;
  spherical.theta = theta;
  const position = new Vector3();
  position.setFromSpherical(spherical);
  return position;
}

/**
 * 绘制底座
 */
export const addHexagon = (scene, position, name, font) => {
  const lookAt = new Vector3(position.x * 2, position.y * 2, position.z * 2);
  const tag = new Group();
  // 圆环底座几何体
  const haxagonGeometry = new RingGeometry(
    (hexagonRadius / 5) * 3,
    hexagonRadius,
    32,
    6,
  );
  const haxagonMatrial = new MeshBasicMaterial({ color: 0xffffff });
  const haxagon = new Mesh(haxagonGeometry, haxagonMatrial);
  tag.add(haxagon);
  tag.properties = { ...position, name };

  // 文字label
  const textGeometry = new TextGeometry(name, {
    font, // — THREE.Font的实例。
    size: hexagonRadius * 1.5, //— Float。字体大小，默认值为100。
    height: 0.01, //— Float。挤出文本的厚度。默认值为50。
    curveSegments: 24, //— Integer。（表示文本的）曲线上点的数量。默认值为12。
    bevelEnabled: true, //— Boolean。是否开启斜角，默认为false。
    bevelThickness: 0.1, //— Float。文本上斜角的深度，默认值为20。
    bevelSize: 0.1, //— Float。斜角与原始文本轮廓之间的延伸距离。默认值为8。
    bevelSegments: 25, //— Integer。斜角的分段数。默认值为3。
  });
  const textMaterail = new MeshBasicMaterial({ color: 0xffffff });
  const text = new Mesh(textGeometry, textMaterail);
  text.position.y = hexagonRadius + 1;
  text.position.x = text.position.x - hexagonRadius * name.length;
  tag.add(text);

  tag.position.copy(position);
  tag.lookAt(lookAt);
  scene.add(tag);
};
