import React, { useEffect } from 'react';
import './style.less';
import { randomHexColor } from '../../utils/common';
const MoveBlock = () => {
  // 水平移动
  const horizontalLoop = (dom) => {
    if (!dom) return;
    const domParent = dom.parentNode;
    let direction = 'right',
      IsFromZero = dom.offsetLeft === 0,
      distance =
        dom.offsetLeft === 0
          ? dom.offsetLeft
          : domParent.clientWidth / 2 - dom.clientWidth / 2; //获取初始化位置
    function move() {
      if (IsFromZero) {
        if (distance >= domParent.clientWidth - dom.clientWidth) {
          direction = 'left';
        } else if (distance <= 0) {
          direction = 'right';
        }
      } else {
        if (distance >= domParent.clientWidth - dom.clientWidth / 2) {
          direction = 'left';
          dom.style.backgroundColor = randomHexColor();
        } else if (distance <= dom.clientWidth / 2) {
          direction = 'right';
          dom.style.backgroundColor = randomHexColor();
        }
      }

      if (direction === 'right') {
        distance++;
      } else {
        distance--;
      }
      dom.style.left = `${distance}px`;

      window.requestAnimationFrame(move);
    }
    window.requestAnimationFrame(move);
  };
  /**
   *
   * @param {*} dom
   * @returns
   */
  const verticalLoop = (dom) => {
    if (!dom) return;
    const domParent = dom.parentNode;
    let direction = 'bottom',
      IsFromZero = dom.offsetTop === 0,
      distance =
        dom.offsetTop === 0
          ? dom.offsetTop
          : domParent.clientHeight / 2 - dom.clientHeight / 2; //获取初始化位置

    function move() {
      if (IsFromZero) {
        // 从顶端开始的
        if (distance >= domParent.clientHeight - dom.clientHeight) {
          direction = 'top';
        } else if (distance <= 0) {
          direction = 'bottom';
        }
      } else {
        if (distance >= domParent.clientHeight - dom.clientHeight / 2) {
          direction = 'top';
          dom.style.backgroundColor = randomHexColor();
        } else if (distance <= dom.clientHeight / 2) {
          direction = 'bottom';
          dom.style.backgroundColor = randomHexColor();
        }
      }

      if (direction === 'bottom') {
        distance++;
      } else {
        distance--;
      }
      dom.style.top = `${distance}px`;
      window.requestAnimationFrame(move);
    }
    window.requestAnimationFrame(move);
  };

  useEffect(() => {
    horizontalLoop(document.querySelector('.horizontal'));
    verticalLoop(document.querySelector('.vertical'));
    verticalLoop(document.querySelector('.shift'));
    horizontalLoop(document.querySelector('.shift'));
  }, []);

  return (
    <div className="moveContent">
      <div className="horizontal">horizontal</div>
      <div className="vertical">vertical</div>
      <div className="shift"></div>
    </div>
  );
};

export default MoveBlock;
