  padding: 0.4rem 1rem;
  overflow: auto;
}
.textSkill .textContainer p {
  margin: 0rem !important;
}
.textSkill .textContainer p {
  color: #000;
  font-size: 0.85rem;
  font-weight: bold;
  font-family: cursive;
  letter-spacing: 0.05rem;
}
.textSkill .cursor {
  width: 0.8rem;
  height: 0.8rem;
  border-radius: 50%;
  background: #8b8b8b;
  animation: blink 0.5s linear infinite;
}
@keyframes blink {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
