import {
  AxesHelper,
  Color,
  PerspectiveCamera,
  Raycaster,
  Scene,
  Vector2,
  WebGLRenderer,
} from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import Stats from 'three/addons/libs/stats.module.js';
import { GUI } from 'three/addons/libs/lil-gui.module.min.js';

class Basic {
  constructor(options) {
    this.dom = options?.dom;
    this.tooltip = options?.tooltip;
    this.option = options;
    this.initSences();
    this.setControls();
    this.setAxesHelper();
    this.setStats();
    this.setGUI();
    window.addEventListener('resize', () => this.resize());
  }

  // 创建场景
  initSences() {
    // 场景
    this.scene = new Scene();
    this.scene.background = new Color(this.option?.background || '#010826');
    // 相机
    this.camera = new PerspectiveCamera(
      this.option.fov || 60,
      this.option.aspect || this.dom.clientWidth / this.dom.clientHeight,
      this.option.near || 0.1,
      this.option.far || 3000,
    );
    // 相机位置
    this.camera.position.set(0, 0, 200);
    // 渲染器
    this.renderer = new WebGLRenderer({ antialias: true, alpha: true });
    this.renderer.setPixelRatio(window.devicePixelRatio);
    this.renderer.setSize(this.dom.clientWidth, this.dom.clientHeight);
    this.dom.append(this.renderer.domElement);
  }

  //三维旋转辅助器
  setControls() {
    this.controls = new OrbitControls(this.camera, this.renderer.domElement);
    this.controls.autoRotateSpeed = 3;
    // 使动画循环使用时阻尼或自转 意思是否有惯性
    this.controls.enableDamping = true;
    // 动态阻尼系数 就是鼠标拖拽旋转灵敏度
    this.controls.dampingFactor = 0.05;
    // 是否可以缩放
    this.controls.enableZoom = true;
    // 设置相机距离原点的最近距离
    this.controls.minDistance = 100;
    // 设置相机距离原点的最远距离
    this.controls.maxDistance = 300;
    // 是否开启右键拖拽
    this.controls.enablePan = true;
  }

  //辅助坐标系
  setAxesHelper() {
    if (this.option.noAxes) return;
    this.axesHelper = new AxesHelper(this.option?.axesSize || 80);
    this.scene.add(this.axesHelper);
  }

  // 设置渲染记录器
  setStats() {
    // if (!this.option.axesHelper) return;
    this.stats = new Stats();
    this.dom.append(this.stats.domElement);
  }

  // 添加Gui
  setGUI() {
    // if (!this.option.axesHelper) return;
    this.gui = new GUI();
  }

  // 自适应
  resize() {
    this.renderer.setSize(this.dom.clientWidth, this.dom.clientHeight);
    this.camera.aspect = this.dom.clientWidth / this.dom.clientHeight;
    this.camera.updateProjectionMatrix();
  }
}

export default Basic;
