import React, { useEffect, useRef, useState } from 'react';
import { Rectangle } from './components/Producer';
import './style.less';
let shapes = [];
// 自定义canvas
const CustomerCanvas = () => {
  const [selectedItem, setSelectItem] = useState(null);
  const animationId = useRef(null); // 使用 useRef 来存储动画 ID
  // 绘制画布
  const drawGraph = () => {
    const ctx = rootRef.current.getContext('2d');
    ctx.clearRect(0, 0, rootRef.current.width, rootRef.current.height);
    for (const shape of shapes) shape.draw(ctx);
    animationId.current = requestAnimationFrame(drawGraph);
  };

  // 获取鼠标节点信息
  const getShape = (x, y) => {
    // 倒着循环
    for (let index = shapes.length - 1; index >= 0; index--) {
      const shape = shapes[index];
      if (shape.isInside(x, y)) return shape;
    }
    return null;
  };

  // 初始化画布
  const initGraph = () => {
    const colorPicker = document.querySelector('.widget');
    const cvsContainer = rootRef.current.parentNode.getBoundingClientRect();
    const w = cvsContainer.width,
      h = cvsContainer.height;
    // 保持清晰度 dpr
    rootRef.current.width = w * devicePixelRatio;
    rootRef.current.height = h * devicePixelRatio;
    Object.assign(rootRef.current.style, { width: w + 'px', height: h + 'px' });

    // 注册canvas 鼠标事件
    rootRef.current.onmousedown = (e) => {
      // 判断按下鼠标的时候，是不是已经有矩形了
      const shape = getShape(e.offsetX, e.offsetY);
      setSelectItem(shape);
      const cvsRect = rootRef.current.getBoundingClientRect();
      let rect = null;
      if (shape) {
        const sx = e.offsetX,
          sy = e.offsetY; // 记录原始坐标
        const { startX, startY, endX, endY } = shape;
        shapes.forEach((item) => {
          item.id === shape.id
            ? item.setBorder(colorPicker.value, '#F5603F', 4)
            : item.setBorder(item.color, null, 1);
        });
        window.onmousemove = (e) => {
          const x = e.clientX - cvsRect.left;
          const y = e.clientY - cvsRect.top;
          const dx = x - sx;
          const dy = y - sy;
          // 获取是不是在父级canvas里面
          if (
            startX + dx >= 0 &&
            startY + dy >= 0 &&
            endX + dx <= cvsRect.right - cvsRect.left &&
            endY + dy <= cvsRect.bottom - cvsRect.top
          ) {
            shape.startX = startX + dx;
            shape.startY = startY + dy;
            shape.endX = endX + dx;
            shape.endY = endY + dy;
          }
        };
      } else {
        shapes?.forEach((item) => item.setBorder(item.color, null, 1));
        rect = new Rectangle(e.offsetX, e.offsetY, colorPicker.value);
        shapes.push(rect);

        // 鼠标移动,改变结束坐标信息
        window.onmousemove = (e) => {
          const x = e.clientX - cvsRect.left;
          const y = e.clientY - cvsRect.top;
          rect.endX = x;
          rect.endY = y;
        };
      }
      // 鼠标抬起,清除事件
      window.onmouseup = () => {
        window.onmousemove = null;
        window.onmouseup = null;
        // 最小绘制10 * 10
        const { startX, startY, endX, endY } = rect;
        if (!(endX - startX >= 10 && endY - startY >= 10))
          shapes = shapes.filter((item) => item?.id !== rect.id);
      };
    };
    drawGraph();
  };

  // 删除图形
  const handleDel = () => {
    shapes = shapes.filter((item) => item?.id !== selectedItem.id);
    setSelectItem(null); // 清除选中的矩形
  };

  // 根节点
  const rootRef = useRef(null);
  useEffect(() => {
    if (!rootRef.current) return;
    initGraph();
    return () => {
      cancelAnimationFrame(animationId.current);
      shapes = [];
    };
  }, [rootRef]);

  // 颜色设置
  const [color, setColor] = useState('');
  useEffect(() => {
    if (!selectedItem) return;
    shapes.forEach((item) => {
      item.id === selectedItem.id
        ? item.setBorder(color)
        : item.setBorder(item.color);
    });
  }, [color]);

  return (
    <div className="customerCanvas">
      <div className="operator">
        <input
          type="color"
          className="widget"
          value={color}
          onChange={(e) => setColor(e.target.value)}
        />
        {selectedItem && (
          <div className="widget del" onClick={handleDel}>
            删除
          </div>
        )}
      </div>
      <div className="graph">
        <canvas ref={rootRef} />
      </div>
    </div>
  );
};

export default CustomerCanvas;
