import React, { useEffect, useState } from 'react';
import './style.less';
import { HomeOutlined } from '@ant-design/icons';
import routes from '../../layout/Header/menu';
import { useNavigate, useLocation } from 'react-router';
import OutsideClickHandler from 'react-outside-click-handler';

/**
 * 可进行拖拽的菜单
 * @returns
 */
const DragMenu = () => {
  // 初始化
  const initial = () => {
    // 获取父级容器信息
    const { parentNode } = document.querySelector('.DragMenu');
    const { offsetLeft: poffsetX, offsetTop: poffsetY } = parentNode;
    const { width: parentNodeWidth, height: parentNodeHeight } =
      parentNode.getBoundingClientRect();

    // 拖动，设置移动块
    const block = document.querySelector('.DragMenu');
    let startX, startY;

    // 鼠标按下
    block.onmousedown = function (e) {
      block.classList.add('grab');
      const { clientX, clientY } = e;
      startX = clientX - block.offsetLeft - poffsetX;
      startY = clientY - block.offsetTop - poffsetY;

      const { width: blockWidth, height: blockHeight } =
        block.getBoundingClientRect();

      // 移动
      document.onmousemove = function (event) {
        block.classList.add('grabbing');
        // 计算位移
        let mleft = event.clientX - startX - poffsetX;
        let mtop = event.clientY - startY - poffsetY;

        const setPosition = function () {
          //  blockWidth / 2 是因为 transform: translate(-50%, -50%); 垂直居中，偏移了一半。
          block.style.left = mleft + 'px';
          block.style.top = mtop + 'px';

          // 边界值处理
          if (mleft <= 0) block.style.left = 0 + 'px';
          if (mleft >= parentNodeWidth - blockWidth)
            block.style.left = parentNodeWidth - blockWidth + 'px';

          if (mtop <= 0) block.style.top = 0 + 'px';
          if (mtop >= parentNodeHeight - blockHeight)
            block.style.top = parentNodeHeight - blockHeight + 'px';

          window.requestAnimationFrame(setPosition);
        };

        window.requestAnimationFrame(setPosition);
      };

      // 结束
      document.onmouseup = function () {
        document.onmousemove = null;
        document.onmouseup = null;
        block.classList.remove('grabbing');
        block.classList.remove('grab');
      };

      return false;
    };
  };

  useEffect(() => {
    initial();
  }, []);

  // 路由切换
  const [active, setActive] = useState(null);
  const navigate = useNavigate();
  const doTrigger = (path) => {
    if (path === active) return;
    setActive(path);
    navigate(path);
  };

  const { pathname } = useLocation();
  useEffect(() => {
    setActive(pathname);
  }, []);

  const [visible, setVisible] = useState(false);
  const showPanel = () => setVisible((pre) => !pre);
  return (
    <div className="DragMenu">
      <OutsideClickHandler onOutsideClick={() => setVisible(false)}>
        <HomeOutlined onDoubleClick={showPanel} />
      </OutsideClickHandler>
      <div className={`menuList ${visible ? 'visible' : ''}`}>
        {routes.map((route, index) => (
          <div
            className={`menuItem ${active === route.path ? 'active' : ''}`}
            onClick={() => doTrigger(route.path)}
            key={index}
          >
            {route.name}
          </div>
        ))}
      </div>
    </div>
  );
};

export default DragMenu;
