  padding: 2px 8px;
  overflow: auto;
}
.FileUpload .container {
  width: 100%;
  height: 13rem;
  margin-bottom: 2rem;
  border: 1px solid #ccc;
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
}
.FileUpload .container .wrapContent {
  position: absolute;
  left: 0;
  top: 0;
  top: var(--parentHeight, 0);
  height: 0;
  height: var(--parentWidth, 0);
  width: 0;
  width: var(--parentHeight, 0);
  transform-origin: left top;
  transform: rotate(-90deg);
  overflow: auto;
}
.FileUpload .container .wrapImg {
  transform: rotate(90deg);
  height: 0;
  height: var(--parentHeight, 0);
  white-space: nowrap;
  /* 禁止子级 div 换行 */
}
.FileUpload .container .wrapImg img,
.FileUpload .container .wrapImg span {
  display: inline-block;
  margin: 0 0.2rem;
  height: 12rem;
  width: 20rem;
  -o-object-fit: contain;
     object-fit: contain;
  border-radius: 0.2rem;
}
.FileUpload .text {
  font-size: 1rem;
  margin-top: 1rem;
  background: linear-gradient(to right, #7e3f2b, #146420) no-repeat right bottom;
  background-size: 0 2px;
  font-weight: bold;
  transition: background-size 0.3s linear;
}
.FileUpload .text:hover {
  background-position: left bottom;
  background-size: 100% 2px;
  cursor: pointer;
}
.FileUpload .modal {
  background: url('https://fastly.picsum.photos/id/7/4728/3168.jpg?hmac=c5B5tfYFM9blHHMhuu4UKmhnbZoJqrzNOP9xjkV4w3o') no-repeat center / cover;
  background-repeat: no-repeat;
  height: 20rem;
  width: 100%;
  margin-top: 1rem;
}
.FileUpload .modal .content {
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
}
.FileUpload .modal .content h1 {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 9rem;
  text-stroke: 1px #fff;
  -webkit-text-stroke: 1px #fff;
  background: url('https://fastly.picsum.photos/id/7/4728/3168.jpg?hmac=c5B5tfYFM9blHHMhuu4UKmhnbZoJqrzNOP9xjkV4w3o') no-repeat center / cover;
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
  margin: 0;
  position: relative;
}
.FileUpload .parallax {
  --d: 360deg;
  width: 22.5rem;
  height: 22.5rem;
  display: grid;
  margin: 0 auto;
  margin-top: 4rem;
  margin-bottom: 4rem;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-template: 'A A B' 'C E B' 'C D D';
  grid-gap: 0.5rem;
  gap: 0.5rem;
  animation: rotation 12s linear infinite;
}
.FileUpload .parallax .item {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.FileUpload .parallax .item:nth-child(1) {
  grid-area: A;
}
.FileUpload .parallax .item:nth-child(2) {
  grid-area: B;
}
.FileUpload .parallax .item:nth-child(3) {
  grid-area: C;
}
.FileUpload .parallax .item:nth-child(4) {
  grid-area: D;
}
.FileUpload .parallax .item:nth-child(5) {
  grid-area: E;
}
.FileUpload .parallax .item img {
  --d: -360deg;
  width: 230%;
  height: 230%;
  -o-object-fit: cover;
     object-fit: cover;
  animation: rotation 12s linear infinite;
}
@keyframes rotation {
  to {
    transform: rotate(var(--d));
  }
}
.FileUpload .gridContainer {
  width: 22.5rem;
  height: 22.5rem;
  margin: 0 auto;
  margin-top: 2rem;
  margin-bottom: 2rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 0.05rem;
  gap: 0.05rem;
  transition: all 0.2s linear;
}
.FileUpload .gridContainer .item {
  display: flex;
  justify-content: center;
  transition: all 0.2s linear;
  align-items: center;
  color: #fff;
  font-size: 1.4rem;
}
.FileUpload .gridContainer .item:hover {
  font-size: 3rem;
  font-weight: bold;
}
.FileUpload .gridContainer:has(.item:nth-child(1):hover) {
  grid-template-columns: 2fr 1fr 1fr;
  font-size: 2rem;
  grid-template-rows: 2fr 1fr 1fr;
}
.FileUpload .gridContainer:has(.item:nth-child(2):hover) {
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: 2fr 1fr 1fr;
}
.FileUpload .gridContainer:has(.item:nth-child(3):hover) {
  grid-template-columns: 1fr 1fr 2fr;
  grid-template-rows: 2fr 1fr 1fr;
}
.FileUpload .gridContainer:has(.item:nth-child(4):hover) {
  grid-template-columns: 2fr 1fr 1fr;
  grid-template-rows: 1fr 2fr 1fr;
}
.FileUpload .gridContainer:has(.item:nth-child(5):hover) {
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: 1fr 2fr 1fr;
}
.FileUpload .gridContainer:has(.item:nth-child(6):hover) {
  grid-template-columns: 1fr 1fr 2fr;
  grid-template-rows: 1fr 2fr 1fr;
}
.FileUpload .gridContainer:has(.item:nth-child(7):hover) {
  grid-template-columns: 2fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 2fr;
}
.FileUpload .gridContainer:has(.item:nth-child(8):hover) {
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: 1fr 1fr 2fr;
}
.FileUpload .gridContainer:has(.item:nth-child(9):hover) {
  grid-template-columns: 1fr 1fr 2fr;
  grid-template-rows: 1fr 1fr 2fr;
}
.FileUpload .textMenu {
  position: absolute;
  display: none;
  display: var(--display, none);
  top: 0;
  top: var(--top, 0);
  left: 0;
  left: var(--left, 0);
  height: 0;
  height: var(--height, 0);
  width: 10rem;
  box-shadow: 0 0.1rem 0.4rem rgba(0, 0, 0, 0.1);
  transition: all 0.2s linear;
  justify-content: center;
  align-items: center;
  z-index: 2;
}
