import React, { useEffect } from 'react';
import DragMenu from '@/components/DragMenu';
import './style.less';
import bootstrap from './threejs';
const Vector = () => {
  useEffect(() => {
    bootstrap();
  }, []);
  return (
    <div className="vector">
      <DragMenu />
      <div id="loading">
        <div className="sk-chase">
          {new Array(6).fill(1).map((dot, index) => (
            <div className="sk-chase-dot" key={index} />
          ))}
        </div>
        <div>加载资源中...</div>
      </div>
      <div id="html2canvas" className="css3d-wapper">
        <div className="fire-div"></div>
      </div>
      <div id="earth-canvas" />
    </div>
  );
};

export default Vector;
