import React, { useEffect, useRef, useState } from 'react';
import './style.less';
import Basic from '../Learn3js/components/Basic';
import chinaJson from '@/static/json/china.json';
import * as THREE from 'three';
import * as d3 from 'd3';
import { randomHexColor } from '@/utils/common';
import { pointLight } from '../Learn3js/components/Light';
import { CNMap, projection } from './components/CNMap';

let lastPick = null,
  callback = [],
  Object = [],
  loopReq;
const FlyLine = () => {
  const root = useRef();
  const [basic, setBasic] = useState();
  // 获取场景、相机、渲染器、控制器
  const bootstrap = () => {
    const canvas = new Basic({
      dom: root.current,
      noAxes: true,
    });
    setBasic(canvas);
  };

  // 开始绘制
  const setup = () => {
    const { scene, camera, renderer } = basic;

    let tooltip = document.querySelector('#tooltip');

    const onMouseMove = (event) => {
      let pickPosition = { x: 0, y: 0 };
      const { top, left, width, height } =
        root.current.parentNode.getBoundingClientRect();
      const clientX = event.clientX - left;
      const clientY = event.clientY - top;
      pickPosition.x = (clientX / width) * 2 - 1;
      pickPosition.y = (clientY / height) * -2 + 1;
      const raycaster = new THREE.Raycaster();

      raycaster.setFromCamera(pickPosition, camera);
      // 计算物体和射线的交点
      const intersects = raycaster.intersectObjects(scene.children, true);

      if (lastPick) {
        lastPick.object.material[0].color.set('#00ccff');
        lastPick.object.material[1].color.set('#fff');
      }
      lastPick = intersects.find(
        (item) => item.object.material && item.object.material.length === 2,
      );
      if (lastPick) {
        lastPick.object.material[0].color.set(randomHexColor());
        lastPick.object.material[1].color.set(randomHexColor());
        tooltip.style.left = clientX + 20 + 'px';
        tooltip.style.top = clientY - 20 + 'px';
        const { properties } = lastPick.object.parent;
        tooltip.innerHTML = properties?.name;
        tooltip.style.visibility = 'visible';
      } else {
        tooltip.style.visibility = 'hidden';
      }
    };
    window.addEventListener('mousemove', onMouseMove);
    // 添加光
    {
      scene.add(pointLight);
    }
    // 添加地图
    {
      scene.add(CNMap);
      Object.push(CNMap);
    }

    /**
     * 绘制连线
     */
    // 圆环网格对象组
    const circleYs = [];
    const spotCircle = (spot) => {
      // 圆
      const geometry = new THREE.CircleGeometry(0.5, 200);
      const material = new THREE.MeshBasicMaterial({
        color: 0xff0000,
        side: THREE.DoubleSide,
      });
      const circle = new THREE.Mesh(geometry, material);
      // 绘制地图时 y轴取反 这里同步
      circle.position.set(spot[0], -spot[1], spot[2] + 0.1);
      scene.add(circle);

      // 圆环
      const geometry2 = new THREE.RingGeometry(0.5, 0.7, 50);
      // transparent 设置 true 开启透明
      const material2 = new THREE.MeshBasicMaterial({
        color: 0xff0000,
        // side: THREE.DoubleSide,
        // transparent: true,
        opacity: 1,
      });
      const circleY = new THREE.Mesh(geometry2, material2);
      scene.add(circleY);
      // 绘制地图时 y轴取反 这里同步
      circleY.position.set(spot[0], -spot[1], spot[2] + 0.1);
      circleYs.push(circleY);
    };

    // 移动物体网格对象组
    const moveSpots = [];
    // 线上的移动物体
    const moveSpot = (curve) => {
      const aGeo = new THREE.SphereGeometry(0.4, 12, 12);
      const aMater = new THREE.MeshPhongMaterial({
        color: randomHexColor(),
        side: THREE.DoubleSide,
      });
      const aMesh = new THREE.Mesh(aGeo, aMater);
      aMesh.curve = curve;
      aMesh._s = 0;
      moveSpots.push(aMesh);
      scene.add(aMesh);
    };

    const lineConnect = (posStart, posEnd) => {
      // 根据目标坐标设置3D坐标  z轴位置在地图表面
      const [x0, y0, z0] = [...posStart, 4];
      const [x1, y1, z1] = [...posEnd, 4];
      // 使用QuadraticBezierCurve3() 创建 三维二次贝塞尔曲线
      const curve = new THREE.QuadraticBezierCurve3(
        new THREE.Vector3(x0, -y0, z0),
        new THREE.Vector3((x0 + x1) / 2, -(y0 + y1) / 2, 20),
        new THREE.Vector3(x1, -y1, z1),
      );
      // 绘制 目标位置
      spotCircle([x0, y0, z0]);
      spotCircle([x1, y1, z1]);
      moveSpot(curve);

      const lineGeometry = new THREE.BufferGeometry();

      // 获取曲线上的50个点
      var points = curve.getPoints(50);
      var positions = [];
      var colors = [];
      var color = new THREE.Color();

      // 给每个顶点设置演示 实现渐变
      for (var j = 0; j < points.length; j++) {
        color.setHSL(0.81666 + j, 0.88, 0.715 + j * 0.25); // 粉色
        colors.push(color.r, color.g, color.b);
        positions.push(points[j].x, points[j].y, points[j].z);
      }

      // 放入顶点 和 设置顶点颜色
      lineGeometry.attributes.position = new THREE.BufferAttribute(
        new Float32Array(positions),
        3,
        true,
      );

      lineGeometry.attributes.color = new THREE.BufferAttribute(
        new Float32Array(colors),
        3,
        true,
      );

      const material = new THREE.LineBasicMaterial({
        vertexColors: THREE.VertexColors,
        side: THREE.DoubleSide,
      });
      const line = new THREE.Line(lineGeometry, material);
      return line;
    };

    scene.add(
      lineConnect(
        projection([106.557691, 29.559296]),
        projection([104.006215, 30.650055]),
      ),
    );

    scene.add(
      lineConnect(
        projection([106.557691, 29.559296]),
        projection([104.006215, 30.650055]),
      ),
    );

    scene.add(
      lineConnect(
        projection([106.557691, 29.559296]),
        projection([116.396795, 39.93242]),
      ),
    );

    scene.add(
      lineConnect(
        projection([106.557691, 29.559296]),
        projection([87.617099, 43.863737]),
      ),
    );

    // 渲染
    function animation() {
      circleYs.forEach(function (mesh) {
        //  目标 圆环放大 并 透明
        mesh._s += 0.01;
        mesh.scale.set(1 * mesh._s, 1 * mesh._s, 1 * mesh._s);
        if (mesh._s <= 2) {
          mesh.material.opacity = 2 - mesh._s;
        } else {
          mesh._s = 1;
        }
      });

      moveSpots.forEach(function (mesh) {
        mesh._s += 0.006;
        let tankPosition = new THREE.Vector3();
        tankPosition = mesh.curve.getPointAt(mesh._s % 1);
        mesh.position.set(tankPosition.x, tankPosition.y, tankPosition.z);
      });

      Object.forEach((group) => {
        // group.rotateY(0.00001);
      });
      renderer.render(scene, camera);
      loopReq = requestAnimationFrame(animation);
    }
    loopReq = requestAnimationFrame(animation);

    callback.push({
      funName: 'mousemove',
      func: onMouseMove,
    });
    window.addEventListener('mousemove', onMouseMove, false);
  };

  useEffect(() => {
    if (!basic) return;
    setup();
  }, [basic]);

  useEffect(() => {
    if (!(root && root.current)) return;
    bootstrap();
    return () => {
      cancelAnimationFrame(loopReq);
      callback.forEach((fn) => window.removeEventListener(fn.funName, fn.func));
    };
  }, [root]);

  return (
    <div className="flyLine">
      <div ref={root} className="jswrap" />
      <div id="tooltip" />
    </div>
  );
};

export default FlyLine;
