import React from 'react';
import { Navigate } from 'react-router';
import LazyLoad from '../../../components/LazyLoad';
import TextSkill from './Tools/components/textSkill';
import Visualization from './Tools/components/visualization';
import CustomerCanvas from './Tools/components/customerCanvas';

const Layout = () => LazyLoad(() => import('@/layout'));
const Tools = () => LazyLoad(() => import('./Tools'));

const routes = [
  {
    path: '/',
    element: <Layout />,
    children: [
      {
        path: '/',
        element: <Tools />,
        children: [
          {
            index: true,
            element: <Navigate to="/business/textskill" />,
          },
          {
            path: '/textSkill',
            element: <TextSkill />,
          },
          {
            path: '/visualization',
            element: <Visualization />,
          },
          {
            path: '/customerCanvas',
            element: <CustomerCanvas />,
          },
          // {
          //   path: '/communication',
          //   element: <Communication />,
          // },
        ],
      },
    ],
  },
];
export default routes;
