  padding: 0.2rem 0.5rem;
  position: relative;
  cursor: pointer;
  color: #888;
  background: #fafafa;
  border: 1px solid #ddd;
  border-radius: 0.2rem;
  overflow: hidden;
  display: inline-block;
  font-size: 0.75rem;
}
.upload input {
  position: absolute;
  right: 0;
  top: 0;
  opacity: 0;
  filter: alpha(opacity=0);
  cursor: pointer;
}
.upload input:hover {
  color: #444;
  background: #eee;
  border-color: #ccc;
  -webkit-text-decoration: none;
  text-decoration: none;
}
