import React, { useEffect, useState, useRef } from 'react';
import './style.less';
import { randomHexColor } from '../../../../../../utils/common';

// 文件上传
const FileUpload = () => {
  const imgList = [
    'https://picsum.photos/300/200?random=1',
    'https://picsum.photos/300/200?random=2',
    'https://picsum.photos/300/200?random=3',
    'https://picsum.photos/300/200?random=4',
    'https://picsum.photos/300/200?random=5',
  ];

  const menuRef = useRef();
  const contentRef = useRef();

  const scrollContent = useRef();

  const createTxtMenu = (event) => {
    event.preventDefault(); // 阻止默认的右键菜单行为
    const { clientX, clientY } = event;
    const { offsetLeft } = contentRef?.current;
    const { offsetTop } = contentRef?.current?.parentNode;
    menuRef?.current.style.setProperty(
      '--top',
      clientY - offsetTop + 10 + 'px',
    );
    menuRef?.current.style.setProperty('height', 0);
    menuRef?.current.style.setProperty(
      '--left',
      clientX - offsetLeft + 10 + 'px',
    );

    menuRef?.current.style.setProperty('height', 100 + 'px');

    menuRef?.current.style.setProperty('--display', 'flex');
  };
  useEffect(() => {
    // 添加右键点击事件监听器
    document.addEventListener('contextmenu', createTxtMenu);
    document.addEventListener('click', function (event) {
      event.preventDefault(); // 阻止默认的右键菜单行为
      menuRef?.current.style.setProperty('--display', 'none');
    });
    return () => {
      document.removeEventListener('contextmenu', createTxtMenu);
    };
  }, []);

  useEffect(() => {
    if (scrollContent && scrollContent?.current) {
      const { clientWidth, clientHeight } = scrollContent?.current.parentNode;
      scrollContent?.current.style.setProperty(
        '--parentWidth',
        clientWidth + 'px',
      );
      scrollContent?.current.style.setProperty(
        '--parentHeight',
        clientHeight + 'px',
      );
    }
  }, [scrollContent]);

  return (
    <div className="FileUpload" ref={contentRef}>
      <div className="container">
        <div className="wrapContent" ref={scrollContent}>
          <div className="wrapImg">
            {new Array(10).fill(1).map((item, index) => (
              // <span key={index} style={{ backgroundColor: randomHexColor() }} />
              <img key={index} src="https://picsum.photos/id/237/400/240" />
              // <img key={index} src={img1} />
            ))}
          </div>
        </div>
      </div>

      {/* 奇妙的下划线 */}
      <span className="text">
        The sun lazily peeked through the curtains, casting gentle rays across
        the room. Birds chirped outside, their melodies weaving through the
        early morning air. With a stretch and a yawn, Sarah rose from her bed,
        feeling the warmth of the sunlight on her skin. Today felt full of
        possibilities, like a blank canvas waiting to be painted with vibrant
        colors. She made her way to the kitchen, the scent of fresh coffee
        enticing her senses. As she sipped her morning brew, she couldn't help
        but smile, grateful for the simple joys that each new day brought.
      </span>
      {/* 蒙版 */}
      <div className="modal">
        <div className="content">
          <h1>AMSTERDAM</h1>
        </div>
      </div>

      {/* 视差旋转 */}
      <div className="parallax">
        {imgList.map((src, index) => (
          <div className="item" key={index}>
            <img src={src} />
          </div>
        ))}
      </div>
      {/* 网格布局动画 */}
      <div className="gridContainer">
        {new Array(9).fill(0).map((rect, index) => (
          <div
            key={index}
            className="item"
            style={{ backgroundColor: randomHexColor() }}
          >
            {index + 1}
          </div>
        ))}
      </div>

      {/* 右键菜单 */}
      <div className="textMenu" ref={menuRef}>
        菜单
      </div>
    </div>
  );
};

export default FileUpload;
