import React from 'react';
import LazyLoad from '../../../components/LazyLoad';
import Jsonp from './Tools/components/Jsonp';
import Carousel from './Tools/components/Carousel';
import Vector from '../../Vector';
import Learn3js from './Tools/components/Learn3js';
import FlyLine from './Tools/components/FlyLine';
import AttackEarth from './Tools/components/AttackEarth';
import UserAction from './Tools/components/UserAction';
import ViewPort from './Tools/components/ViewPort';
import FileUpload from './Tools/components/FileUpload';
import { Navigate } from 'react-router';

const Layout = () => LazyLoad(() => import('@/layout'));
const Tools = () => LazyLoad(() => import('./Tools'));

const routes = [
  {
    path: '/',
    element: <Layout />,
    children: [
      {
        path: '/',
        element: <Tools />,
        children: [
          {
            index: true,
            element: <Navigate to="/entrance/Jsonp" />,
          },
          // {
          //   index: true,
          //   element: <Jsonp />,
          // },
          {
            path: '/Jsonp',
            element: <Jsonp />,
          },
          {
            path: '/carousel',
            element: <Carousel />,
          },
          {
            path: '/learn3js',
            element: <Learn3js />,
          },
          {
            path: '/flyLine',
            element: <FlyLine />,
          },
          {
            path: '/earth',
            element: <AttackEarth />,
          },
          {
            path: '/userAction',
            element: <UserAction />,
          },
          {
            path: '/viewPort',
            element: <ViewPort />,
          },
          {
            path: '/upload',
            element: <FileUpload />,
          },
        ],
      },
    ],
  },
];
export default routes;
