import { uniqueId } from 'lodash';

/**
 * 矩形构造器
 */
export class Rectangle {
  constructor(startX, startY, color) {
    this.startX = startX;
    this.startY = startY;
    this.color = color;
    this.endX = startX;
    this.endY = startY;
    this.id = uniqueId();
  }

  get minX() {
    return Math.min(this.startX, this.endX);
  }

  get maxX() {
    return Math.max(this.startX, this.endX);
  }

  get minY() {
    return Math.min(this.startY, this.endY);
  }

  get maxY() {
    return Math.max(this.startY, this.endY);
  }
  /**
   * 绘制
   * @param {*} ctx
   */
  draw(ctx) {
    ctx.fillStyle = this.color;
    ctx.fillRect(
      this.minX * devicePixelRatio,
      this.minY * devicePixelRatio,
      (this.maxX - this.minX) * devicePixelRatio,
      (this.maxY - this.minY) * devicePixelRatio,
    );

    // 绘制边框
    ctx.strokeStyle = this.borderColor || '#fff';
    ctx.lineWidth = (this.borderWidth || 1) * devicePixelRatio;
    ctx.strokeRect(
      this.minX * devicePixelRatio,
      this.minY * devicePixelRatio,
      (this.maxX - this.minX) * devicePixelRatio,
      (this.maxY - this.minY) * devicePixelRatio,
    );
  }

  /**
   * 判断坐标点是不是图形中
   * @param {*} x
   * @param {*} y
   */
  isInside(x, y) {
    return x >= this.minX && x <= this.maxX && y >= this.minY && y <= this.maxY;
  }

  setBorder(fillColor, color = null, width = 1) {
    this.borderColor = color;
    this.borderWidth = width;
    this.color = fillColor;
  }

  setFillColor(color) {
    this.color = color;
  }
}
