import React, { useEffect, useState } from 'react';
import xhr from '@/components/JSONP';
import './style.less';

const Jsonp = (props) => {
  const [info, setInfo] = useState(null);
  // 获取数据信息
  const loadData = async () => {
    try {
      const option = {
        url: '/api/jsonpTest',
        data: null,
      };
      const resp = await xhr(option);
      setInfo(resp);
    } catch (error) {
      console.log(error);
      setInfo(error);
    }
  };

  return (
    <div className="jsonp">
      <span className="btn" onClick={loadData}>
        点击执行Jsonp
      </span>
      {info && <div>{info?.message || info?.type}</div>}
    </div>
  );
};

export default Jsonp;
