import React, { useEffect } from 'react';
import { SliderNative } from '@/components/Slider';
import './style.less';
import MoveBlock from '../../../../../../components/MoveBlock';
import DragBlock from '../../../../../../components/DragBlock';
import Upload from '../../../../../../components/Upload';
const Carousel = () => {
  useEffect(() => {
    SliderNative(document.querySelector('.Carousel'));
    DragBlock(document.querySelector('.marquee'));
  }, []);
  return (
    <div className="Carousel">
      <MoveBlock />
      {/* CSS渐变下划线效果 */}
      <h2 className="title">
        <span>CSS渐变下划线linear-gradient效果</span>
      </h2>
      <div className="marquee">
        <p className="marqueeText">
          坚定不移的永远跟党走，一直跟党走，高举习总书记思想文化精神！
        </p>
      </div>
      {/* 文件上传 */}
      <Upload />
    </div>
  );
};

export default Carousel;
