import React, { useEffect, useRef, useState, useCallback } from 'react';
import './style';
import * as THREE from 'three';
import { randomHexColor } from '@/utils/common';
import Basic from './components/Basic';
import { CNMap, rayList } from './components/Graph';
import { pointLight, directionalLight } from './components/Light';
let loopReq,
  lastPick,
  objectCuve = [];
// 可视化
const Visualization = props => {
  const root = useRef();
  // 画布实例
  const [instance, setInstance] = useState();
  useEffect(() => {
    if (!(root && root.current)) return;
    setInstance(new Basic({ dom: root.current, background: '#060c2a' }));
  }, [root]);

  // 绘制
  const render = () => {
    const { scene, camera, renderer } = instance;
    const animation = () => {
      objectCuve.forEach(group => {
        group.rotateY(0.04);
      });
      renderer.render(scene, camera);
      loopReq = requestAnimationFrame(animation);
    };

    loopReq = requestAnimationFrame(animation);
  };

  const [pickInfo, setPickInfo] = useState();
  // 鼠标滑动事件处理
  const handleMouse = useCallback(
    event => {
      event.preventDefault();
      if (!instance) return;
      const { camera, canvas, scene } = instance;
      const tooltip = document.querySelector('.tooltips');
      const parentDom = tooltip.parentNode.getBoundingClientRect();
      const { clientX, clientY } = event;
      const mouse = new THREE.Vector2();
      const { left, top, width, height } = canvas?.getBoundingClientRect();
      mouse.x = ((clientX - left) / width) * 2 - 1;
      mouse.y = -((clientY - top) / height) * 2 + 1;
      // 光线投射用于进行鼠标拾取
      const vector = new THREE.Vector3(mouse.x, mouse.y, 1).unproject(camera);
      const raycaster = new THREE.Raycaster();
      raycaster.setFromCamera(mouse, camera); // 更新原点坐标和相机视椎
      const intersects = raycaster.intersectObjects(scene.children, true); // 检查射线与场景对象和其子集是否有交集

      if (lastPick) {
        lastPick.object.material[0].color.set('#0b96a5');
        lastPick.object.material[0].opacity = 0.4;
        lastPick.object.material[1].color.set('#0b96a5');
        lastPick.object.material[1].opacity = 0.8;
        setPickInfo(null);
      }

      // 替换新的交互点
      if (intersects.length) {
        // 2个material，表明就是省份
        lastPick = intersects.find(
          item => item.object.material && item.object.material.length === 2
        );
        if (!lastPick?.object?.parent) {
          tooltip.style.visibility = 'hidden';
        } else {
          lastPick.object.material[0].color.set('#057ada');
          lastPick.object.material[1].color.set('#057ada');
          const { properties } = lastPick?.object?.parent;
          const { adcode, name, centroid } = properties;
          setPickInfo({ adcode, name, centroid });
          tooltip.style.visibility = 'visible';
          tooltip.style.left = clientX - parentDom?.left + 10 + 'px';
          tooltip.style.top = clientY - parentDom?.top + 'px';
        }
      } else {
        tooltip.style.visibility = 'hidden';
      }
    },
    [instance]
  );

  // 启动
  const setup = () => {
    const { scene } = instance;
    // 添加光照
    {
      scene.add(pointLight);
      scene.add(directionalLight);
    }
    // 添加中国地图
    {
      scene.add(CNMap);
    }
    // 设置省份名称
    {
      scene.add(rayList);
    }
    render();
  };

  // 清空画布
  const removeChildren = () => {
    const { scene } = instance;
    if (scene?.children) {
      scene.children.forEach(children => {
        if (children.type === 'Mesh') {
          children.geometry.dispose();
          children.material.dispose();
        }
        scene.remove(children);
      });
    }
  };

  useEffect(() => {
    if (!instance) return;
    const { canvas } = instance;
    setup();
    canvas?.addEventListener?.('mousemove', handleMouse);
    return () => {
      cancelAnimationFrame(loopReq);
      canvas?.removeEventListener?.('mousemove', handleMouse);
      removeChildren();
    };
  }, [instance]);

  return (
    <div className="visualization">
      {/* 画布 */}
      <div ref={root} className="canvasDom" />
      {/* 提示框 */}
      <div className="tooltips">
        <div>
          <span>攻击源：</span>
          <span>{pickInfo?.name || '--'}</span>
        </div>
        <div>
          <span>攻击目的：</span>
          <span>{pickInfo?.name || '--'}</span>
        </div>
        <div>
          <span>区位编号：</span>
          <span>{pickInfo?.adcode || '--'}</span>
        </div>
      </div>
    </div>
  );
};

export default Visualization;
