  position: relative;
  height: 100%;
}
.vector #loading {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
  background: #010826;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #409eff;
  font-size: 15px;
  letter-spacing: 2px;
  overflow: hidden;
}
.vector #loading.out {
  animation: zoomOut 0.5s linear forwards;
  pointer-events: none;
}
.vector #loading.out .sk-chase-dot,
.vector #loading.out .sk-chase {
  animation: null;
}
@keyframes zoomOut {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
    transform: scale3d(1.3, 1.3, 1.3);
  }
  to {
    opacity: 0;
  }
}
.vector .sk-chase {
  margin-bottom: 20px;
  width: 40px;
  height: 40px;
  position: relative;
  animation: sk-chase 2.5s infinite linear both;
}
.vector .sk-chase-dot {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  animation: sk-chase-dot 2s infinite ease-in-out both;
}
.vector .sk-chase-dot::before {
  content: '';
  display: block;
  width: 20%;
  height: 20%;
  background-color: #409eff;
  border-radius: 100%;
  animation: sk-chase-dot-before 2s infinite ease-in-out both;
}
.vector .sk-chase-dot:nth-child(1) {
  animation-delay: -1.1s;
}
.vector .sk-chase-dot:nth-child(2) {
  animation-delay: -1s;
}
.vector .sk-chase-dot:nth-child(3) {
  animation-delay: -0.9s;
}
.vector .sk-chase-dot:nth-child(4) {
  animation-delay: -0.8s;
}
.vector .sk-chase-dot:nth-child(5) {
  animation-delay: -0.7s;
}
.vector .sk-chase-dot:nth-child(6) {
  animation-delay: -0.6s;
}
.vector .sk-chase-dot:nth-child(1):before {
  animation-delay: -1.1s;
}
.vector .sk-chase-dot:nth-child(2):before {
  animation-delay: -1s;
}
.vector .sk-chase-dot:nth-child(3):before {
  animation-delay: -0.9s;
}
.vector .sk-chase-dot:nth-child(4):before {
  animation-delay: -0.8s;
}
.vector .sk-chase-dot:nth-child(5):before {
  animation-delay: -0.7s;
}
.vector .sk-chase-dot:nth-child(6):before {
  animation-delay: -0.6s;
}
.vector .sk-chase-dot .sk-chase-dot:nth-child(2) {
  animation-delay: -1s;
}
.vector .sk-chase-dot:nth-child(3) {
  animation-delay: -0.9s;
}
.vector .sk-chase-dot:nth-child(4) {
  animation-delay: -0.8s;
}
.vector .sk-chase-dot:nth-child(5) {
  animation-delay: -0.7s;
}
.vector .sk-chase-dot:nth-child(6) {
  animation-delay: -0.6s;
}
.vector .sk-chase-dot:nth-child(1):before {
  animation-delay: -1.1s;
}
.vector .sk-chase-dot:nth-child(2):before {
  animation-delay: -1s;
}
.vector .sk-chase-dot:nth-child(3):before {
  animation-delay: -0.9s;
}
.vector .sk-chase-dot:nth-child(4):before {
  animation-delay: -0.8s;
}
.vector .sk-chase-dot:nth-child(5):before {
  animation-delay: -0.7s;
}
.vector .sk-chase-dot:nth-child(6):before {
  animation-delay: -0.6s;
}
@keyframes sk-chase {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes sk-chase-dot {
  80%,
  100% {
    transform: rotate(360deg);
  }
}
@keyframes sk-chase-dot-before {
  50% {
    transform: scale(0.4);
  }
  100%,
  0% {
    transform: scale(1);
  }
}
.vector h1,
.vector h2 {
  position: absolute;
  color: #fff;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 20vh;
  pointer-events: none;
}
.vector h2 {
  pointer-events: all;
  padding-top: 25vh;
}
.vector h2 a {
  color: #1316e2;
  font-size: 30px;
}
.vector #html2canvas {
  position: absolute;
  z-index: -1;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0);
}
.vector .css3d-wapper {
  pointer-events: none;
  color: #fff;
}
.vector .fire-div {
  font-size: 20px;
  font-weight: 600;
  border-top: 3px solid #0cd1eb;
  padding: 6px 8px;
  min-width: 50px;
  background: rgba(40, 108, 181, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.vector #earth-canvas {
  height: 100%;
  width: 100%;
  background: #010826;
}
.vector body {
  overflow: hidden;
  position: relative;
}
