import React, { useEffect, useRef, useState } from 'react';
import * as THREE from 'three';
import { GUI } from 'three/addons/libs/lil-gui.module.min.js';
import './style.less';
import { randomHexColor } from '@/utils/common';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import Basic from './components/Basic';
import {
  directionalLight,
  directionalLightHelper,
  pointLight,
  pointLightHelper
} from './components/Light';
import { lines, points } from './components/BufferGeometry';
import { CNMap, hearMesh } from './components/ExtrudeGeometry';
import { ballMesh, curve, curveMesh, starsMesh } from './components/Curve';
const Object = [];

const obj = {
  scale: 0,
  bool: false,
  color: '#ffffff',
  specular: 0x111111, //材质高光颜色
  pointColor: randomHexColor(),
  lineColor: randomHexColor()
};

let callback = [],
  loopReq;

const Learn3js = () => {
  const root = useRef();
  const [basic, setBasic] = useState();
  // 获取场景、相机、渲染器、控制器
  const bootstrap = () => {
    const canvas = new Basic({
      dom: root.current
    });
    setBasic(canvas);
  };

  // 开始绘制
  const setup = () => {
    const { scene, camera, renderer, stats, gui } = basic;
    {
      const boxGeometry = new THREE.BoxGeometry(40, 40, 40);
      // const boxMaterial = new THREE.MeshBasicMaterial({
      //   color: randomHexColor(),
      // });
      // const boxMaterial = new THREE.MeshLambertMaterial({
      //   color: randomHexColor(),
      // });
      const boxMaterial = new THREE.MeshPhongMaterial({
        color: randomHexColor(),
        specular: '#00ccff',
        side: THREE.DoubleSide,
        flatShading: true
      });
      const boxMesh = new THREE.Mesh(boxGeometry, boxMaterial);
      boxMesh.position.set(20, 10, 30);
      // scene.add(boxMesh);
      // Object.push(boxMesh);

      const matGui = gui.addFolder('材质');
      matGui
        .addColor(obj, 'color')
        .name('材质颜色')
        .onChange(value => {
          boxMaterial.color.set(value);
        });
      matGui
        .addColor(obj, 'specular')
        .name('材质高光颜色')
        .onChange(value => {
          boxMaterial.specular.set(value);
        });

      matGui.close();
    }

    // 添加光
    {
      scene.add(pointLight);
      // scene.add(pointLightHelper);
      const pointLightGui = gui.addFolder('点光照');
      pointLightGui.add(pointLight, 'intensity', 0, 20).name('点光照强度');
      pointLightGui.add(pointLight.position, 'x', 0, 100).name('点光照x');
      pointLightGui.add(pointLight.position, 'y', 0, 100).name('点光照y');
      pointLightGui.add(pointLight.position, 'z', 0, 100).name('点光照z');
      pointLightGui.close();

      scene.add(directionalLight);
      // scene.add(directionalLightHelper);
      const directionalLightGui = gui.addFolder('平行光照');
      directionalLightGui
        .add(directionalLight, 'intensity', 0, 20)
        .name('平行光照强度');
      directionalLightGui
        .add(directionalLight.position, 'x', 0, 100)
        .name('平行光照x');
      directionalLightGui
        .add(directionalLight.position, 'y', 0, 100)
        .name('平行光照y');
      directionalLightGui
        .add(directionalLight.position, 'z', 0, 100)
        .name('平行光照z');
      directionalLightGui.close();
    }
    // 缓冲几何体
    {
      // scene.add(points);

      const bufferGui = gui.addFolder('缓冲材质');
      bufferGui
        .addColor(obj, 'pointColor')
        .name('点材质颜色')
        .onChange(val => {
          points.material.color.set(val);
          points.material.size = Math.random() * 10;
        });

      // scene.add(lines);
      bufferGui
        .addColor(obj, 'lineColor')
        .name('线材质颜色')
        .onChange(val => {
          lines.material.color.set(val);
          lines.material.size = Math.random() * 10;
        });
    }

    // 形状体
    {
      // scene.add(hearMesh);
      // Object.push(hearMesh);
      scene.add(CNMap);
    }

    {
      scene.add(ballMesh);
      scene.add(curveMesh);
      scene.add(starsMesh);
    }

    // 鼠标最后放置的地方
    let lastPick = null;
    const raycaster = new THREE.Raycaster();
    const mouse = new THREE.Vector2();
    let tooltip = document.querySelector('#tooltip');
    const onMouseMove = event => {
      // 获取容器信息
      const { top, left, width, height } = root.current.getBoundingClientRect();
      let clientX = event.clientX - left; // 减去偏移量
      let clientY = event.clientY - top;
      mouse.x = (clientX / width) * 2 - 1;
      mouse.y = -(clientY / height) * 2 + 1;
      raycaster.setFromCamera(mouse, camera); // 更新原点坐标和相机视椎
      const intersects = raycaster.intersectObjects(scene.children, true); // 检查射线与场景对象和其子集是否有交集
      tooltip.style.left = clientX + 20 + 'px';
      tooltip.style.top = clientY - 20 + 'px';
      if (lastPick) {
        lastPick.object.material[0].color.set('#00ccff');
        lastPick.object.material[0].opacity = 0.4;
        lastPick.object.material[1].color.set('#fff');
        lastPick.object.material[1].opacity = 0.8;
      }
      lastPick = intersects.find(
        item => item.object.material && item.object.material.length === 2
      );

      if (lastPick) {
        lastPick.object.material[0].color.set(0xffffff);
        lastPick.object.material[1].color.set(0xffffff);
        const { properties } = lastPick.object.parent;
        tooltip.innerHTML = properties?.name;
        tooltip.style.visibility = 'visible';
      } else {
        tooltip.style.visibility = 'hidden';
      }
    };
    let rate = 0;
    // 渲染
    function animation() {
      Object.forEach(group => {
        group.rotateY(-0.1);
      });

      rate += 0.005;
      const vector = curve.getPoint(rate % 1);
      ballMesh.position.set(vector.x, vector.y, vector.z);
      renderer.render(scene, camera);
      loopReq = requestAnimationFrame(animation);
    }
    loopReq = requestAnimationFrame(animation);

    callback.push({
      funName: 'mousemove',
      func: onMouseMove
    });
    window.addEventListener('mousemove', onMouseMove, false);
  };

  useEffect(() => {
    if (!basic || !(root && root.current)) return;
    setup();
  }, [basic]);

  useEffect(() => {
    if (!(root && root.current)) return;
    bootstrap();
    return () => {
      cancelAnimationFrame(loopReq);
      callback.forEach(fn => window.removeEventListener(fn.funName, fn.func));
    };
  }, [root]);

  return (
    <div className="learn3js">
      <div ref={root} className="jswrap" />
      <div id="tooltip" />
    </div>
  );
};

export default Learn3js;
