___CSS_LOADER_EXPORT___.push([module.id, `.Carousel {
  position: relative;
  padding: 0.2rem 0.6rem;
}
.Carousel .title span {
  font-size: 0.75rem;
  background: linear-gradient(to right, #ec695c, #61c454) no-repeat right bottom;
  transition: background-size 0.4s;
  background-size: 0% 0%;
  cursor: pointer;
}
.Carousel .title span:hover {
  background-position: left bottom;
  background-size: 100% 0.1rem;
}
.Carousel .marquee {
  position: absolute;
  width: 10rem;
  aspect-ratio: 7.5;
  border-radius: 0.2rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background: #c51605;
  overflow: hidden;
  cursor: pointer;
  z-index: 2;
  right: 10%;
  box-shadow: 0 0.1rem 0.4rem rgba(0, 0, 0, 0.3);
}
.Carousel .marquee .marqueeText {
  position: absolute;
  white-space: nowrap;
  font-size: 0.76rem;
  color: #ffe17b;
  top: 50%;
  left: 100%;
  transform: translate(0%, -50%);
  animation: 6s linear 0.2s infinite none running slidein;
}
@keyframes slidein {
  0% {
    left: 100%;
  }
  100% {
    left: -210%;
  }
}
