import React from 'react';
import { createRoot } from 'react-dom/client';
import {
  BrowserRouter,
  HashRouter,
  Navigate,
  Route,
  Routes,
} from 'react-router-dom';
import { ConfigProvider } from 'antd';
import zhCN from 'antd/es/locale/zh_CN';
import moment from 'moment';
import { Provider } from 'react-redux';
import 'moment/locale/zh-cn';
import 'antd/dist/antd.css';
import { registerMicroApps, start } from 'qiankun';
import microApp from './micro-app';
import store from './redux/store';
import Entrance from './pages/Entrance';
import Qiankun from './pages/Qiankun';
import Vector from './pages/Vector';
import Business from './pages/Business';
import ErrorBoundary from './components/ErrorBoundary';
import { setRem } from './utils/common';
moment.locale('zh-cn');
import './style';

// 入口模块
// import Wedding from './wedding';
// import native from './native';
// import three from './three';
// 容器
const container = document.querySelector('#root');
// 启动
const bootstrap = () => {
  try {
    createRoot(container).render(
      <ErrorBoundary>
        <ConfigProvider locale={zhCN}>
          <Provider store={store}>
            <HashRouter>
              <Routes>
                <Route path="/" element={<Navigate to="/business" />} />
                <Route path="/business/*" element={<Business />} />
                <Route path="/entrance/*" element={<Entrance />} />
                <Route path="/micro/*" element={<Qiankun />} />
                <Route path="/vector/*" element={<Vector />} />
              </Routes>
            </HashRouter>
          </Provider>
        </ConfigProvider>
      </ErrorBoundary>,
    );
    /**
     * 添加事件监听函数
     */
    setRem(container);
    window.addEventListener('resize', () => setRem(container));
    window.addEventListener('unhandledrejection', function (event) {
      // 这个事件对象有两个特殊的属性：
      console.log(event.promise); // [object Promise] —— 生成该全局 error 的 promise
      console.log(event.reason); // Error —— 未处理的 error 对象
    });
    window.addEventListener('beforeunload', () => {
      window.removeEventListener('resize', () => setRem(container));
      window.removeEventListener('unhandledrejection');
    });
  } catch (error) {
    console.log(error);
  }
};

bootstrap();

// 微服务注册
export const startApp = () => {
  registerMicroApps(microApp, {
    // 生命周期函数信息
    beforeLoad: (app) => console.log('before load app.name====>>>>>', app.name),
    beforeMount: [
      (app) =>
        console.log('[LifeCycle] before mount %c%s', 'color: green;', app.name),
    ],
    afterMount: [
      (app) =>
        console.log('[LifeCycle] after mount %c%s', 'color: green;', app.name),
    ],
    afterUnmount: [
      (app) =>
        console.log(
          '[LifeCycle] after unmount %c%s',
          'color: green;',
          app.name,
        ),
    ],
  });
  start({ sandbox: { prefetch: 'all' } });

  // start({
  //   sandbox: {
  //     // 默认开启预加载
  //     prefetch: 'all',
  //     // qiankun提供的样式隔离方法（严格模式）
  //     strictStyleIsolation: true,
  //     experimentalStyleIsolation: true,
  //   },
  // });
};
