import {
  CanvasTexture,
  ClampToEdgeWrapping,
  FrontSide,
  Group,
  LinearFilter,
  Mesh,
  MeshBasicMaterial,
  MeshPhysicalMaterial,
  MeshStandardMaterial,
  NearestFilter,
  Scene,
  SphereGeometry,
  TextureLoader,
} from 'three';
import earthday from '../assets/earthday.jpg';
import bumpMap from '../assets/8k_earth_normal_map.jpg';
import earthnight from '../assets/8k_earth_nightmap.jpg';
import cloudsMap from '../assets/8k_earth_clouds.jpg';
import earthRoughness from '../assets/earth2.jpg';
import GlowMaterial from './GlowMaterial';

const defaultTextureParams = {
  wrapT: ClampToEdgeWrapping,
  wrapS: ClampToEdgeWrapping,
  minFilter: NearestFilter,
  magFilter: LinearFilter,
  generateMipmaps: false, // Mipmaps 是一系列逐渐缩小的纹理图像，通常用于提高纹理映射的性能和视觉质量
};

const dealTexture = function (texture, params = {}) {
  const newParams = { ...defaultTextureParams, ...params };
  Object.keys(newParams).forEach((key) => (texture[key] = newParams[key]));
};

const createEarthObject = function ({ radius, nightDom, areaDom }) {
  const scene = new Group();
  /**
   * 加载纹理贴图
   */
  const earthMap = new TextureLoader().load(earthday);
  dealTexture(earthMap);

  const earthBumpMap = new TextureLoader().load(bumpMap);
  dealTexture(earthBumpMap);

  const earthNightMap = new TextureLoader().load(earthnight);
  dealTexture(earthNightMap);

  const roughnessMap = new TextureLoader().load(earthRoughness);
  dealTexture(roughnessMap);

  /**
   * 绘制地球
   */
  const earthGeometry = new SphereGeometry(radius, 100, 100);
  const earthMaterial = new MeshPhysicalMaterial({
    map: earthMap,
    transparent: true,
    side: FrontSide,
    depthTest: true,
    depthWrite: true,
    alphaTest: 0,
    emissive: 0x000000,
    roughness: 1,
    metalness: 0,
    reflectivity: 0.8,
    specularIntensity: 0.2,
    specularColor: 0xffffff,
    fog: true,
    clearcoat: 0,
    clearcoatRoughness: 0,
    clearcoatRoughnessMap: roughnessMap,
  });
  const earthMesh = new Mesh(earthGeometry, earthMaterial);
  earthMesh.receiveShadow = true;
  earthMesh.castShadow = true;
  scene.add(earthMesh);

  /**
   * Canvas纹理 陆地、海洋
   */
  const nightGeometry = new SphereGeometry(radius + 0.1, 100, 100);
  const nightTexture = new CanvasTexture(nightDom);
  dealTexture(nightTexture);
  const nightMaterial = new MeshBasicMaterial({
    map: earthNightMap,
    alphaMap: nightTexture,
    transparent: true,
    side: FrontSide,
  });
  const nightMesh = new Mesh(nightGeometry, nightMaterial);
  nightMesh.receiveShadow = true;
  nightMesh.castShadow = true;
  scene.add(nightMesh);

  const areaGeometry = new SphereGeometry(radius + 0.2, 100, 100);
  const areaTexture = new CanvasTexture(areaDom);
  dealTexture(areaTexture);
  const areaMaterial = new MeshBasicMaterial({
    map: earthNightMap,
    alphaMap: areaTexture,
    transparent: true,
    side: FrontSide,
  });
  const areaMesh = new Mesh(areaGeometry, areaMaterial);
  areaMesh.receiveShadow = true;
  areaMesh.castShadow = true;
  scene.add(areaMesh);

  // 云层
  const cloudsGeometry = new SphereGeometry(radius + 0.3, 100, 100);
  const cloudsTexture = new TextureLoader().load(cloudsMap);
  dealTexture(cloudsTexture);
  const cloudsMaterial = new MeshStandardMaterial({
    color: 0xffffff,
    alphaMap: cloudsTexture,
    transparent: true,
    opacity: 1,
  });
  const cloudsMesh = new Mesh(cloudsGeometry, cloudsMaterial);
  scene.add(cloudsMesh);

  // 幽灵
  const glowGeometry = new SphereGeometry(radius + 0.4, 100, 100);
  const glowMaterial = GlowMaterial();
  const glowMesh = new Mesh(glowGeometry, glowMaterial);
  scene.add(glowMesh);

  return {
    earth: scene,
    nightTexture,
    areaTexture,
  };
};

export { createEarthObject };
