const getActiveRule = (hash) => (location) => location.hash.startsWith(hash);
const microApp = [
  {
    name: 'microWedding',
    entry:
      process.env.NODE_ENV === 'development'
        ? 'http://localhost:9009'
        : 'http://leeckq.gitee.io/wedding', // 子项目对应的地址， 打包之后存放对应的网址
    activeRule: '#/micro',
    container: '#microApp', // 子应用挂载的div
    props: {
      rerenderForLegacy: true,
      forceRefresh: true,
    },
    fetch: window.fetch,
  },
];

export default microApp;
