import React, { useEffect, useState, useRef } from 'react';
import './style.less';
import loading from './assets/loading.svg';
import spin from './assets/spin.svg';
import { throttle } from '@/utils/common';
import oceans from './assets/oceans.mp4';

let instance;
const Lazyload = () => {
  // 懒加载回调函数
  const callback = entries => {
    entries.forEach(entry => {
      // 判断是否与视口交叉
      const { isIntersecting } = entry;
      if (isIntersecting) {
        const img = entry.target;
        img.src = img.dataset.src;
        instance.unobserve(img);
      }
    });
  };

  // 瀑布流无限加载
  const [count, setCount] = useState(10);
  const waterCallback = throttle(entries => {
    entries.forEach(entry => {
      const { isIntersecting } = entry;
      if (isIntersecting) {
        setCount(prev => prev + 10);
      }
    });
  }, 300);

  // 视频流回调
  const playerCallback = throttle(entries => {
    entries.forEach(entry => {
      const { isIntersecting } = entry;
      if (isIntersecting) {
        videoRef?.current?.play();
      } else {
        videoRef?.current?.pause();
      }
    });
  }, 300);

  useEffect(() => {
    document.querySelectorAll('.fallContent img[data-src]')?.forEach(dom => {
      setTimeout(() => {
        if (dom.dataset.src) dom.src = dom.dataset.src;
        delete dom.dataset.src;
      }, 200);
    });
  }, [count]);

  const videoRef = useRef();

  useEffect(() => {
    // 动态加载
    // instance = new IntersectionObserver(callback);
    // document
    //   .querySelectorAll('img[data-src]')
    //   ?.forEach((dom) => instance.observe(dom));
    // 瀑布流
    instance = new IntersectionObserver(waterCallback, {
      threshold: 0.9
    });
    instance.observe(document.querySelector('.spin'));

    // 视频播放
    if (!(videoRef && videoRef?.current)) return;
    const videoDom = new IntersectionObserver(playerCallback, {
      threshold: 0.9
    });
    videoDom.observe(videoRef.current);
    return () => {
      instance.disconnect();
      videoDom.disconnect();
    };
  }, [videoRef]);

  return (
    <div className="lazyLoadWrap">
      {new Array(0).fill(1).map((item, index) => (
        <div className="item" key={index}>
          <img
            src={loading}
            data-src={`https://picsum.photos/400/600?r=${index + 1}`}
            alt=""
          />
        </div>
      ))}
      {/* 控制视频播放 */}
      <video
        className="video"
        autoPlay={true}
        muted={true}
        controls
        loop
        ref={videoRef}
      >
        <source src={oceans} type="video/mp4" />
      </video>
      <h2 className="textskill">textskill</h2>
      {/* 瀑布流无限加载 */}
      <div className="waterfall">
        <div className="fallContent">
          {new Array(count).fill(1).map((item, index) => (
            <div className="item" key={index}>
              <img
                src={loading}
                data-src={`https://picsum.photos/400/600?r=${index + 1}`}
                alt=""
              />
            </div>
          ))}
        </div>
        <img src={spin} className="spin" />
      </div>
    </div>
  );
};

export default Lazyload;
