import React, { useEffect, useState } from 'react';
import './style.less';
import sliceUpload from './utils/slice';
import concurrencyUpload from './utils/concurrency';
import resumeUpload from './utils/resume';

const size = 1024 * 50; //50KB 切片大小
const Upload = () => {
  const [loading, setLoading] = useState(false);

  const upload = async (e) => {
    const [file] = e.target?.files;
    if (!file) return;
    let fileChunks = [],
      index = 0;
    for (let cur = 0; cur < file.size; cur += size) {
      fileChunks.push({
        hash: index++,
        chunk: file.slice(cur, cur + size),
      });
    }
    const fileInfo = { filename: file?.name, fileChunks };
    setLoading(true);
    try {
      // 切片上传
      // await sliceUpload(fileInfo);
      // 并发控制上传
      await concurrencyUpload(fileInfo);
      // 断点续传
      // await resumeUpload(fileInfo);
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <a onChange={upload} className="upload">
      <input type="file" />
      {loading ? '文件上传中...' : '点击上传文件'}
    </a>
  );
};

export default Upload;
