import {
  BufferAttribute,
  BufferGeometry,
  Color,
  Float32BufferAttribute,
  Line,
  LineBasicMaterial,
  Mesh,
  MeshBasicMaterial,
  Points,
  PointsMaterial,
  QuadraticBezierCurve3,
  SphereGeometry,
  Vector3,
} from 'three';
import { randomHexColor } from '@/utils/common';

const curve = new QuadraticBezierCurve3(
  new Vector3(-30, 0, 0),
  new Vector3(20, 15, 40),
  new Vector3(10, 0, 0),
);

// 要将曲线划分为的分段数。默认是 5
const points = curve.getPoints(50);
const gometry = new BufferGeometry().setFromPoints(points);
const material = new LineBasicMaterial({ color: 0xffffff });
const curveMesh = new Line(gometry, material);
curveMesh.name = '曲线';

const ballGeometry = new SphereGeometry(0.5);
const ballMaterial = new MeshBasicMaterial({ color: randomHexColor() });
const ballMesh = new Mesh(ballGeometry, ballMaterial);
ballMesh.position.set(10, 0, 0);

const positions = [];
const colors = [];
const starGeometry = new BufferGeometry();
for (let i = 0; i < 5000; i++) {
  const vector = new Vector3();
  vector.x = Math.random() * 800 - 100;
  vector.y = Math.random() * 800 - 100;
  vector.z = Math.random() * 800 - 100;
  positions.push(vector.x, vector.y, vector.z);
  const color = new Color();
  color.setHSL(Math.random() * 0.2 + 0.5, 0.55, Math.random() * 0.25 + 0.55);
  // colors.push(randomHexColor(), randomHexColor(), randomHexColor());
  colors.push(color);
}
starGeometry.setAttribute(
  'position',
  new BufferAttribute(new Float32Array(positions), 3),
);
starGeometry.setAttribute(
  'color',
  new BufferAttribute(new Float32Array(colors), 3),
);

const starMaterial = new PointsMaterial({
  side: 2,
  sizeAttenuation: true,
  transparent: true,
  color: 0x4d76cf,
  opacity: 1,
});

const starsMesh = new Points(starGeometry, starMaterial);

starsMesh.scale.set(1, 1, 1);

export { curve, starsMesh, curveMesh, ballMesh };
