import {
  AdditiveBlending,
  BufferGeometry,
  Color,
  DoubleSide,
  Float32BufferAttribute,
  Points,
  ShaderMaterial,
} from 'three';

class InitFlyLine {
  constructor({ texture } = opt) {
    this.flyId = 0; //id
    this.flyArr = []; //存储所有飞线
    this.baicSpeed = 1; //基础速度

    this.flyShader = {
      vertexshader: `
                  uniform float size;
                  uniform float time;
                  uniform float u_len;
                  uniform float isShadow;
                  attribute float u_index;
                  varying float u_opacitys;
                  void main() {
                      if( u_index < time + u_len && u_index > time){
                          float u_scale = 1.0 - (time + u_len - u_index) /u_len;
                          if (isShadow == 1.0) {
                            u_opacitys = 1.0;
                          } else {
                            u_opacitys = u_scale;
                          }
                          vec4 mvPosition = modelViewMatrix * vec4(position, 1.0);
                          gl_Position = projectionMatrix * mvPosition;
                          gl_PointSize = size * u_scale * 300.0 / (-mvPosition.z);
                      }
                  }
                  `,
      fragmentshader: `
                  uniform float u_opacity;
                  uniform vec3 color;
                  varying float u_opacitys;
                  void main() {
                      vec4 u_color = vec4(color,u_opacity * u_opacitys);
                      gl_FragColor = u_color;
                  }`,
    };
  }
  /**
   * [addFly description]
   *
   * @param   {String}  opt.color  [颜色_透明度]
   * @param   {Array}   opt.curve  [线的节点]
   * @param   {Number}  opt.width  [宽度]
   * @param   {Number}  opt.length [长度]
   * @param   {Number}  opt.speed  [速度]
   * @param   {Number}  opt.repeat [重复次数]
   * @return  {Mesh}               [return 图层]
   */
  addFly({
    color = 'rgba(255,255,255,1)',
    curve = [],
    width = 1,
    haloWidth = 6,
    length = 10,
    speed = 1,
    repeat = 1,
    data = {},
    callback,
  }) {
    const colorArr = this.getColorArr(color);
    const geometry = new BufferGeometry();
    const material = new ShaderMaterial({
      uniforms: {
        color: {
          value: new Color(0xffffff),
          type: 'v3',
        },
        size: {
          value: width * 1.5,
          type: 'f',
        },
        u_len: {
          value: length,
          type: 'f',
        },
        u_opacity: {
          value: 1.0,
          type: 'f',
        },
        time: {
          value: -length,
          type: 'f',
        },
        isShadow: {
          value: 0.0,
          type: 'f',
        },
        s: { type: 'f', value: -1.0 },
        b: { type: 'f', value: 0.0 },
        p: { type: 'f', value: 3.0 },
        glowColor: { type: 'c', value: new Color(0xff0000) },
      },
      transparent: true,
      depthTest: true,
      depthWrite: false,
      vertexShader: this.flyShader.vertexshader,
      fragmentShader: this.flyShader.fragmentshader,
      side: DoubleSide,
      blending: AdditiveBlending,
    });

    const shadowGeometry = new BufferGeometry();
    const shadowMaterial = new ShaderMaterial({
      uniforms: {
        color: {
          value: colorArr[0],
          type: 'v3',
        },
        size: {
          value: haloWidth * 4,
          type: 'f',
        },
        u_len: {
          value: length,
          type: 'f',
        },
        u_opacity: {
          value: 0.2,
          type: 'f',
        },
        time: {
          value: -length,
          type: 'f',
        },
        isShadow: {
          value: 1.0,
          type: 'f',
        },
        s: { type: 'f', value: -1.0 },
        b: { type: 'f', value: 0.0 },
        p: { type: 'f', value: 3.0 },
        glowColor: { type: 'c', value: new Color(0xff0000) },
      },
      transparent: true,
      depthTest: true,
      depthWrite: false,
      vertexShader: this.flyShader.vertexshader,
      fragmentShader: this.flyShader.fragmentshader,
      side: DoubleSide,
      // blending: THREE.AdditiveBlending
    });

    const [position, u_index] = [[], []];
    // 飞行线处理
    curve.forEach(function (elem, index) {
      position.push(elem.x, elem.y, elem.z);
      u_index.push(index);
    });
    // 设置图元组成的位置信息
    geometry.setAttribute('position', new Float32BufferAttribute(position, 3));
    // 设置组成图元的每个点的索引信息
    geometry.setAttribute('u_index', new Float32BufferAttribute(u_index, 1));
    // 同理阴隐
    shadowGeometry.setAttribute(
      'position',
      new Float32BufferAttribute(position, 3),
    );
    shadowGeometry.setAttribute(
      'u_index',
      new Float32BufferAttribute(u_index, 1),
    );

    // 创造飞线点(其实就是飞线)并设置属性
    const mesh = new Points(geometry, material);
    const shadowMesh = new Points(shadowGeometry, shadowMaterial);
    mesh.name = 'fly';
    mesh._flyId = this.flyId;
    mesh._speed = speed;
    mesh._repeat = repeat;
    mesh._been = 0;
    mesh._total = curve.length;
    mesh._callback = callback;

    shadowMesh.name = 'fly-shadow';
    shadowMesh._flyId = this.flyId + '-shadow';
    shadowMesh._speed = speed;
    shadowMesh._repeat = repeat;
    shadowMesh._been = 0;
    shadowMesh._total = curve.length;
    shadowMesh._callback = callback;
    mesh.tooltipObject = data;

    this.flyId++;
    this.flyArr.push(mesh); //存储生成的线信息
    mesh.add(shadowMesh);
    return mesh;
  }

  getColorArr(str) {
    if (Array.isArray(str)) return str; //error
    const _arr = [];
    str = str + '';
    str = str.toLowerCase().replace(/\s/g, '');
    // 正则表达式的目标是匹配形如 "rgba()" 或 "()" 的字符串，并捕获其中的内容。
    // 例如，如果字符串为 "rgba(255, 0, 0)"，正则表达式将匹配整个字符串，并捕获 "rgba" 到第一个左括号之间的部分，以及左括号后的所有内容。
    // 如果字符串为 "()"，则只会匹配整个字符串，而捕获组中的内容将是空字符串。
    if (/^((?:rgba)?)\(\s*([^\)]*)/.test(str)) {
      const arr = str.replace(/rgba\(|\)/gi, '').split(',');
      const hex = [
        pad2(Math.round(arr[0] * 1 || 0).toString(16)),
        pad2(Math.round(arr[1] * 1 || 0).toString(16)),
        pad2(Math.round(arr[2] * 1 || 0).toString(16)),
      ];
      _arr[0] = this.color('#' + hex.join(''));
      _arr[1] = Math.max(0, Math.min(1, arr[3] * 1 || 0));
    } else if (str === 'transparent') {
      _arr[0] = this.color();
      _arr[1] = 0;
    } else {
      _arr[0] = this.color(str);
      _arr[1] = 1;
    }
    function pad2(c) {
      return c.length === 1 ? '0' + c : '' + c;
    }
    return _arr;
  }

  color(c) {
    return new Color(c);
  }

  /**
   * [remove 删除]
   * @param   {Object}  mesh  [当前飞线]
   */
  remove(mesh) {
    mesh.material.dispose();
    mesh.geometry.dispose();
    mesh?.children?.[0]?.material?.dispose?.();
    mesh?.children?.[0]?.geometry?.dispose?.();
    mesh.remove(mesh.children[0]);
    this.flyArr = this.flyArr.filter((elem) => elem._flyId !== mesh._flyId);
    mesh?.parent?.remove?.(mesh);
    mesh = null;
  }

  reset() {
    this.flyArr.forEach((elem) => {
      if (!elem.parent) return;
      this.remove(elem);
    });
    this.flyId = 0;
  }

  /**
   * 飞行
   * @param {*} delta
   * @returns
   */
  animation(delta = 0.015) {
    if (delta > 0.2) return;
    this.flyArr.forEach((elem) => {
      if (!elem.parent) return;
      if (elem._been > elem._repeat) {
        elem.visible = false;
        if (typeof elem._callback === 'function') {
          elem._callback(elem);
        }
        this.remove(elem);
      } else {
        const uniforms = elem.material.uniforms;
        const childUniforms = elem?.children?.[0]?.material.uniforms;
        //完结一次
        if (uniforms.time.value < elem._total) {
          uniforms.time.value += delta * (this.baicSpeed / delta) * elem._speed;
          childUniforms.time.value +=
            delta * (this.baicSpeed / delta) * elem._speed;
        } else {
          elem._been += 1;
          elem.children[0]._been += 1;
          uniforms.time.value = -uniforms.u_len.value;
          childUniforms.time.value = -childUniforms.u_len.value;
        }
      }
    });
  }
}

export default InitFlyLine;
