___CSS_LOADER_EXPORT___.push([module.id, `.DragMenu {
  position: absolute;
  width: 1.7rem;
  aspect-ratio: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 2%;
  right: 10%;
  z-index: 9999;
  border-radius: 50%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0 0.1rem 0.2rem rgba(0, 0, 0, 0.1);
  background: #ffbc6a;
}
.DragMenu svg {
  font-size: 0.9rem;
  color: #fff;
}
.DragMenu .menuList {
  position: absolute;
  left: 2.1rem;
  top: 0;
  padding: 0.1rem 0.4rem;
  display: flex;
  flex-direction: column;
  width: 5rem;
  cursor: pointer;
  font-size: 0.8rem;
  font-family: MarryCN;
  box-shadow: 0 0.1rem 0.2rem rgba(255, 255, 255, 0.1);
  transform: scale(0);
  opacity: 0;
  background-color: #fff;
  transition: all 0.5s linear;
  border-radius: 0.4rem;
}
.DragMenu .menuList .menuItem {
  padding: 0.1rem 0rem;
}
.DragMenu .menuList .active {
  color: #ffbc6a;
}
.DragMenu .visible {
  opacity: 1;
  transform: scale(1);
}
.grab {
  cursor: grab;
}
.grabbing {
  cursor: grabbing;
}
