import React, {
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import './style.less';
import flightLine from './flightLine.json';
import { createSceneObject } from './components/SceneObject';
import { YELLOW_RED_ANGLE, radius } from './components/GlobalConfig';
import { createEarthObject } from './components/EarthObject';
import { Group, Raycaster, Spherical, Vector2, Vector3 } from 'three';
import {
  addHexagon,
  createPosition,
  drawNightEarthAlphaMap,
} from './components/tools';

// 加载JSON格式的字体的类。返回font, 返回值是表示字体的Shape类型的数组
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader';
import InitFlyLine from './components/InitFlyLine';
import { earthAddFlyLine } from './components/FlyLine';

/**
 * 飞行攻击线
 */
const AttackEarth = (props) => {
  const { type = 'lines' } = props;
  // 初始化：数据处理、字体处理
  const [data, setData] = useState();
  const [tagFont, setTagFont] = useState(null);
  useEffect(() => {
    const { pointed, lined } = flightLine || {};
    const attackInfo = {
      points:
        pointed?.map?.((item) => ({
          name: item.name,
          position: [+item.long, +item.lat],
        })) || [],
      lines: lined?.map?.((item) => ({
        line: [
          { position: [+item.from_long, +item.from_lat] },
          { position: [+item.to_long, +item.to_lat] },
        ],
      })),
    };
    setData(attackInfo);
    // 字体加载
    const loader = new FontLoader();
    loader.load('static/font/tagfont.json', (font) => {
      setTagFont(font);
    });
  }, []);

  /**
   * 地球相关
   */
  const containerRef = useRef(); // 容器
  const threeAttackRef = useRef(); // 画布

  const width = useMemo(
    () => containerRef?.current?.clientWidth,
    [containerRef.current],
  );
  const height = useMemo(
    () => containerRef?.current?.clientHeight,
    [containerRef.current],
  );

  //step1:初始化画布信息
  const {
    scene,
    camera,
    renderer,
    control,
    ambientLight,
    directionalLight,
    clock,
    canvas,
  } = useMemo(() => {
    if (!width || !height) return {};
    return createSceneObject({ radius, width, height });
  }, [width, height]);

  //step2:绘制地球原型
  const nightDom = useMemo(() => document.querySelector('#ns-night-earth'), []);
  const areaDom = useMemo(() => document.querySelector('#ns-geo-area'), []);
  const { earth, nightTexture } = useMemo(() => {
    if (!nightDom || !areaDom) return;
    return createEarthObject({ radius, nightDom, areaDom });
  }, [radius]);

  //step3:渲染画布信息
  const earthRender = useCallback(() => {
    if (
      !(
        clock &&
        renderer &&
        scene &&
        control &&
        camera &&
        directionalLight &&
        nightDom
      )
    )
      return;

    const nightCtx = nightDom.getContext('2d');
    const delta = clock.getDelta();
    control.update(delta);
    const cameraVector = camera.position.clone();
    const cameraSpherical = new Spherical();
    cameraSpherical.setFromCartesianCoords(
      cameraVector.x,
      cameraVector.y,
      cameraVector.z,
    );
    drawNightEarthAlphaMap(
      nightCtx,
      cameraSpherical.theta,
      cameraSpherical.phi,
    );
    if (nightTexture) nightTexture.needsUpdate = true;

    /**
     * 设置平行光的位置
     */
    const directionalLightSpherical = new Spherical();
    directionalLightSpherical.radius = 2 * radius;
    directionalLightSpherical.phi = cameraSpherical.phi - YELLOW_RED_ANGLE;
    directionalLightSpherical.theta = cameraSpherical.theta - (Math.PI * 1) / 2;
    const directionalLightPosition = new Vector3();
    directionalLightPosition.setFromSpherical(directionalLightSpherical);
    directionalLight.position.set(
      directionalLightPosition.x,
      directionalLightPosition.y,
      directionalLightPosition.z,
    );
    renderer.render(scene, camera);

    // 渲染，滚动
  }, [clock, renderer, scene, control, camera, directionalLight, nightDom]);

  const requestAnimationFrameAttackRef = useRef();
  useEffect(() => {
    if (
      !(
        scene &&
        camera &&
        renderer &&
        control &&
        ambientLight &&
        directionalLight &&
        clock &&
        earth
      )
    )
      return;

    scene.add(earth);
    scene.add(ambientLight);
    scene.add(directionalLight);
    scene.add(attackLines); // 飞线
    scene.add(rayList); // 位置底座
    // 添加物体
    if (threeAttackRef.current) threeAttackRef.current.innerHTML = '';
    threeAttackRef?.current?.append?.(canvas);

    /**
     * 帧渲染
     */
    const animate = () => {
      earthRender();
      // if (flyManager) flyManager?.animation?.();
      if (attackLines.userData['flyLineArray']?.length) {
        attackLines.userData['flyLineArray']?.forEach((fly) => {
          fly.rotation.z += 0.005; // 调节飞线速度
          if (fly.rotation.z >= fly.flyEndAngle) fly.rotation.z = 0;
        });
      }
      requestAnimationFrameAttackRef.current =
        window.requestAnimationFrame(animate);
    };

    /**
     * 画布自适应
     */
    const resize = function () {
      const newWidth = threeAttackRef.current?.clientWidth;
      const newHeight = threeAttackRef.current?.clientHeight;
      renderer.setSize(newWidth, newHeight);
      camera.aspect = newWidth / newHeight;
      camera.updateProjectionMatrix();
    };

    /**
     * 初始化缩放
     */
    const initialZoom = () => {
      renderer.setSize(width, height);
      camera.aspect = width / height;
      camera.zoom = 0.75;
      camera.updateProjectionMatrix();
    };

    window.addEventListener('resize', resize);
    canvas?.addEventListener?.('dblclick', toogleAutoRotate);
    canvas?.addEventListener?.('mousemove', handleMouse);

    initialZoom();
    animate();
    // 已启动
    setSceneReady(true);

    return () => {
      window.removeEventListener('resize', resize);
      canvas?.removeEventListener?.('dblclick', toogleAutoRotate);
      canvas?.removeEventListener?.('mousemove', handleMouse);
      if (requestAnimationFrameAttackRef.current) {
        cancelAnimationFrame(requestAnimationFrameAttackRef.current);
      }
      if (scene && scene.remove && scene.children) {
        removeChildren(scene);
      }
    };
  }, [
    scene,
    camera,
    renderer,
    control,
    ambientLight,
    directionalLight,
    clock,
    earth,
  ]);

  // 清空画布
  const removeChildren = (object) => {
    if (object.children) {
      object.children.forEach((children) => {
        if (children.type === 'Mesh') {
          children.geometry.dispose();
          children.material.dispose();
        }
        object.remove(children);
      });
    }
  };

  // 画布双击事件,控制轨道是不是旋转
  let autoRotate = useRef(true).current;
  const setAutoRotate = (val) => (autoRotate = !!val);
  const toogleAutoRotate = useCallback(() => {
    control.autoRotate = !autoRotate;
    setAutoRotate(!autoRotate);
  }, [control, autoRotate]);

  // 鼠标滑动事件处理
  const handleMouse = useCallback(
    (event) => {
      if (!(control && canvas && type === 'lines')) return;
      event.preventDefault();
      const { clientX, clientY } = event;
      const mouse = new Vector2();
      const { left, top, width, height } = canvas.getBoundingClientRect();
      mouse.x = ((clientX - left) / width) * 2 - 1;
      mouse.y = -((clientY - top) / height) * 2 + 1;
      // unproject() 方法将在视图空间（屏幕坐标）中的坐标转换为世界空间中的坐标。
      // 这在进行拾取（picking）操作时特别有用，
      // 拾取是通过屏幕上的鼠标位置找到与之对应的场景中的物体

      // UV 坐标主要用于描述纹理映射
      // 世界空间坐标是在全局三维空间中描述物体、相机等位置的坐标
      const vector = new Vector3(mouse.x, mouse.y, 1).unproject(camera);

      // 光线投射用于进行鼠标拾取
      const raycaster = new Raycaster(
        camera.position, // 光线投射的原点向量
        vector.sub(camera.position).normalize(), // 向射线提供方向的方向向量
        0, // 返回的所有结果比near远
        Math.sqrt(
          camera.position.x ** 2 +
            camera.position.y ** 2 +
            camera.position.z ** 2,
        ), // 返回的所有结果都比far近
      );

      // 定义鼠标与相交对象(rayList?.children)底座
      const intersects = raycaster.intersectObjects(rayList?.children || []);
      if (intersects.length > 0) {
        control.autoRotate = autoRotate && false;
        const selectedObject = intersects[0];
        const infoObject = findTooltipObject(selectedObject?.object) || [];
        // setTipPosition({ clientX, clientY });
        // setTooltipObject(infoObject);
        // setTooltipVisible(true);
      } else {
        // setTooltipVisible(false);
        control.autoRotate = autoRotate;
      }
    },
    [control, canvas, type],
  );

  const findTooltipObject = (object) => {
    if (object.type === 'Mesh') {
      console.log(object);
    }

    if (Object.keys(object).includes('tooltipObject')) {
      console.log(object);
      return object.tooltipObject;
    }
    if (object.parent) {
      return findTooltipObject(object.parent);
    }
    return null;
  };

  // step4:添加飞线信息、位置坐标底座
  const [sceneReady, setSceneReady] = useState(false);
  const attackLines = useMemo(() => new Group(), []);
  const flyManager = useMemo(() => new InitFlyLine({}), []);
  const rayList = useMemo(() => new Group(), []);
  useEffect(() => {
    if (type === 'lines' && sceneReady && tagFont) {
      const { lines, points } = data || {};
      // 绘制飞线
      attackLines.userData['flyLineArray'] = [];
      earthAddFlyLine(attackLines, lines || [], flyManager);
      // 绘制底座
      points.map((point) => {
        addHexagon(
          rayList,
          createPosition(point.position, radius + 0.1),
          point.name,
          tagFont,
        );
      });
    }

    // if (type === 'areas' && sceneReady && areaDom) {
    // }
    return () => clearData();
  }, [JSON.stringify(data), sceneReady, scene, areaDom, type, tagFont]);

  /**
   * 清除数据
   */
  const clearData = useCallback(() => {
    try {
      flyManager.reset();
      rayList?.children.forEach((child) => {
        child?.traverse?.((object) => {
          object?.geometry?.dispose?.();
          object?.material?.map?.dispose?.();
          object?.material?.dispose?.();
        });
      });
      rayList.children = [];
    } catch (error) {
      console.log(error);
    }
  }, [rayList]);

  return (
    <div className="attackEarth">
      <div className="container" ref={containerRef}>
        <div
          id="three-attackearth"
          className="threeEarth"
          ref={threeAttackRef}
        />
      </div>
    </div>
  );
};

export default AttackEarth;
