  padding: 0.4rem 1rem;
  display: flex;
  flex-direction: column;
}
.customerCanvas .operator {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 4px 0;
}
.customerCanvas .operator .widget {
  margin-right: 10px;
}
.customerCanvas .operator .widget:nth-last-child(1) {
  margin-right: 0;
}
.customerCanvas .operator .del {
  font-size: 0.75rem;
  cursor: pointer;
  letter-spacing: 0.05rem;
  color: #f5603f;
}
