// 挤压缓冲几何体
import {
  BufferGeometry,
  ExtrudeGeometry,
  Line,
  Mesh,
  MeshBasicMaterial,
  MeshPhongMaterial,
  Object3D,
  Shape,
  Vector3,
} from 'three';
import chinaJson from '@/static/json/china.json';
import * as d3 from 'd3';
import { randomHexColor } from '@/utils/common';

const heartShape = new Shape();
heartShape.moveTo(25, 25);
heartShape.bezierCurveTo(25, 25, 20, 0, 0, 0);
heartShape.bezierCurveTo(-30, 0, -30, 35, -30, 35);
heartShape.bezierCurveTo(-30, 55, -10, 77, 25, 95);
heartShape.bezierCurveTo(60, 77, 80, 55, 80, 35);
heartShape.bezierCurveTo(80, 35, 80, 0, 50, 0);
heartShape.bezierCurveTo(35, 0, 25, 25, 25, 25);

const extrudeSettings = {
  depth: 1,
  bevelEnabled: false,
  bevelSegments: 2,
  steps: 0,
  bevelSize: 0,
  bevelThickness: 0,
};

const heartGeometry = new ExtrudeGeometry(heartShape, extrudeSettings);
const hearMesh = new Mesh(
  heartGeometry,
  new MeshPhongMaterial({
    color: 'red',
    transparent: true,
    opacity: 0.9,
  }),
);

/***
 * 绘制中国地图
 */
const CNMap = new Object3D();
// // 墨卡托投影转换(以北京为中心点)
const projection = d3
  .geoMercator()
  .center([116.23128, 40.22077])
  .translate([0, 0]);
chinaJson.features.forEach((elem) => {
  const province = new Object3D();
  province.name = elem.name;
  //  每一个省份的坐标的处理
  elem?.geometry?.coordinates.forEach((multiPolygon) => {
    // 绘制一个省份的信息
    multiPolygon.forEach((polygon) => {
      const shape = new Shape();
      const points = [];
      for (let index = 0; index < polygon.length; index++) {
        const [x, y] = projection(polygon[index]);
        if (index === 0) {
          shape.moveTo(x, -y);
        }
        shape.lineTo(x, -y);
        points.push(new Vector3(x, -y, 0)); // 一个位于三维空间中的点。
      }

      // 绘制省份轮廓
      const lineMaterial = new MeshBasicMaterial({ color: randomHexColor() });
      const lineGeometry = new BufferGeometry();
      // 通过点队列设置该 BufferGeometry 的 attribute。
      lineGeometry.setFromPoints(points);
      const line = new Line(lineGeometry, lineMaterial);
      province.add(line);

      // 填充每一个省份
      const extrGeometry = new ExtrudeGeometry(shape, {
        depth: 2,
        bevelEnabled: false,
      });
      const material = new MeshPhongMaterial({
        color: '#00ccff',
        transparent: true,
        opacity: 0.4,
      });

      const material2 = new MeshPhongMaterial({
        color: '#fff',
        transparent: true,
        opacity: 0.8,
      });

      const provinceMesh = new Mesh(extrGeometry, [material, material2]);
      // 将省份的属性 加进来
      province.properties = elem.properties;
      province.add(provinceMesh);
    });
  });

  CNMap.add(province);
});

export { hearMesh, CNMap };
