___CSS_LOADER_EXPORT___.push([module.id, `.moveContent {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  height: 50%;
  z-index: 2;
}
.moveContent .horizontal,
.moveContent .vertical,
.moveContent .shift {
  position: absolute;
  z-index: 2;
  padding: 0.2rem 0.6rem;
  font-family: Romance;
  background: #00ccff;
  color: #fff;
  font-size: 1rem;
  box-shadow: 0 0.1rem 0.4rem rgba(0, 0, 0, 0.3);
  border-radius: 0.2rem;
}
.moveContent .horizontal:hover,
.moveContent .vertical:hover,
.moveContent .shift:hover {
  animation-play-state: paused;
  cursor: pointer;
}
.moveContent .horizontal {
  top: 50%;
  left: 0%;
  transform: translate(0%, -50%);
  animation-name: horizontal;
}
.moveContent .vertical {
  top: 0%;
  left: 0%;
  transform: translate(0%, 0%);
  animation-name: vertical;
}
.moveContent .shift {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url(https://s1.ax1x.com/2023/08/15/pPQm9oT.jpg);
  background-blend-mode: lighten;
  background-size: contain;
  width: 11rem;
  height: 5rem;
}
@keyframes horizontal {
  0% {
    left: 0%;
    transform: translate(0%, -50%);
  }
  100% {
    left: 100%;
    transform: translate(-100%, -50%);
  }
}
@keyframes vertical {
  0% {
    top: 0%;
    transform: translate(0%, 0%);
  }
  100% {
    top: 100%;
    transform: translate(0%, -100%);
  }
}
