import React, { useEffect, useState } from 'react';
import './style.less';
// 数字转中文大写
const ToChineseNum = () => {
  const [number, setNumber] = useState();
  const handleChange = (e) => {
    const { value } = e?.target;
    if (/^\d*$/.test(value)) setNumber(value);
  };

  const handleZero = (str) => {
    return str.replace(/零+/g, '零').replace(/零$/, '');
  };

  const [CNCount, setCNCount] = useState('');
  const handleBlur = () => {
    const parts = number
      .toString()
      .replace(/(?=(\d{4})+$)/g, ',')
      .split(',')
      .filter(Boolean);
    const map = ['零', '壹', '贰', '叁', '肆', '伍', '陆', '柒', '捌', '玖'];
    const units = ['', '拾', '佰', '仟'];
    function _transform(n) {
      let result = '';
      for (const i in n) {
        let unit = units[n.length - i - 1];
        let char = map[n[i]];
        // 零的处理
        if (char === '零') unit = '';
        result += char + unit;
      }
      result = handleZero(result);
      return result;
    }

    const bigUnits = ['', '万', '亿'];
    let result = '';
    for (const i in parts) {
      const char = _transform(parts[i]);
      if (char === '') {
        result += '零';
        continue;
      }
      let unit = bigUnits[parts.length - i - 1];
      result += char + unit;
    }
    result = handleZero(result);
    setCNCount(result);
  };

  return (
    <div className="toChineseNum">
      数字金额转大写：
      <input
        value={number}
        onChange={handleChange}
        onBlur={handleBlur}
        placeholder="请输入金额信息"
      />
      {CNCount && <div className="amount">{CNCount}</div>}
    </div>
  );
};

export default ToChineseNum;
