import axios from './axios';

/**
 * 请求拦截器
 */
axios.interceptor.request.use((res) => {
  const token = localStorage.getItem('authToken');
  res.headers = {
    Accept: 'application/json',
    'Accept-Language': localStorage.language || 'zh',
    'Local-Server': true,
  };
  if (token) res.headers['authorization'] = `Token ${token}`;
  return res;
});

/**
 * 响应拦截器
 */
axios.interceptor.response.use(
  (response) => {
    const { data } = response;
    return data;
  },
  (error) => {
    const { status, data } = error;
    data && data?.message && console.error(data?.message);
    switch (status) {
      case 400:
        return Promise.reject(error);
      case 401:
        window.location.href = '/login';
        return Promise.reject(error);
      case 403:
        return Promise.reject(error);
      case 404:
        return Promise.reject(error);
      case 500:
        return Promise.reject(error);
      default:
        return Promise.reject(error);
    }
  },
);

export default axios;
