___CSS_LOADER_EXPORT___.push([module.id, `.lazyLoadWrap {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.lazyLoadWrap .item {
  width: 14rem;
  height: 18rem;
  border-radius: 0.2rem;
  margin: 0.2rem;
  margin-bottom: 0.8rem;
  border: 1px solid #ccc;
  padding: 1rem 0.4rem;
}
.lazyLoadWrap .item img {
  height: 100%;
  width: 100%;
  -o-object-fit: contain;
     object-fit: contain;
  border-radius: 0.2rem;
}
.lazyLoadWrap .waterfall {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.lazyLoadWrap .waterfall .fallContent {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}
.lazyLoadWrap .waterfall .spin {
  width: 3rem;
  animation: rotate 1s linear infinite;
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
    /* 开始时的角度 */
  }
  100% {
    transform: rotate(360deg);
    /* 结束时的角度 */
  }
}
.lazyLoadWrap .video {
  width: 100%;
  margin-bottom: 1rem;
}
.lazyLoadWrap .textskill {
  position: absolute;
  color: #fff;
  text-transform: uppercase;
  font-size: 10rem;
  letter-spacing: 0.1rem;
  font-family: monospace;
  font-weight: bold;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  mix-blend-mode: difference;
}
