import '../style.less';
import { randomHexColor } from '@/utils';

let timer;

/**
 * 原生js实现轮播器
 * @param {*} container  父级容器
 * @param {*} pages 页面个数
 * @param {*} active 活动页
 * @param {*} delay 轮播间隙
 * @returns
 */
const SliderNative = (container, pages = 6, active = 0, delay = 4 * 1000) => {
  if (!container) return;
  // 创建最外层包裹容器
  const viewport = document.createElement('div');
  viewport.classList.add('viewport');

  // 创建滑动容器
  const slider = document.createElement('div');
  slider.classList.add('slider');

  // 创建左右指示按钮
  const LPointer = document.createElement('div');
  LPointer.classList.add('LPointer');
  LPointer.innerHTML = '&lt';

  const RPointer = document.createElement('div');
  RPointer.classList.add('RPointer');
  RPointer.innerHTML = '&gt';

  // 创建指示器
  const pointer = document.createElement('div');
  pointer.classList.add('pointer');

  // 创建滚动页面
  const framgement = document.createDocumentFragment();
  const framgementPointer = document.createDocumentFragment();
  for (let i = 0; i < pages; i++) {
    const content = document.createElement('div');
    content.innerText = +i + 1;
    content.style.backgroundColor = randomHexColor();
    content.classList.add('content');
    framgement.append(content);
    // 指示标
    const point = document.createElement('span');
    point.classList.add('point');
    point.dataset.index = +i;
    framgementPointer.append(point);
    if (active === +i) point.classList.add('active');
  }

  pointer.append(framgementPointer);
  slider.append(framgement);

  // 添加容器
  viewport.append(LPointer);
  viewport.append(RPointer);
  viewport.append(slider);
  viewport.append(pointer);
  container.append(viewport);

  /**
   * 事件处理
   */

  // 滑动
  const doSlider = () => {
    slider.style.transform = `translateX(-${active * 100}%)`;
    document.querySelectorAll('.point').forEach((dom, index) => {
      if (dom.classList.contains('active')) dom.classList.remove('active');
      index === active && dom.classList.add('active');
    });
  };

  // 下一页
  const nextSlide = () => {
    if (active === pages - 1) {
      active = 0;
    } else {
      active++;
    }
    doSlider();
  };
  // 左右手柄点击事件
  RPointer.onclick = () => nextSlide();
  LPointer.onclick = () => {
    if (active === 0) {
      active = pages - 1;
    } else {
      active--;
    }
    doSlider();
  };
  // 指示器点击事件
  viewport.onclick = (event) => {
    if (event.target.dataset?.index !== undefined) {
      active = +event.target.dataset?.index;
      doSlider();
    }
  };

  // 自动轮播
  const loop = () => {
    timer = setTimeout(function doLoop() {
      nextSlide();
      timer = setTimeout(doLoop, delay);
    }, delay);
  };
  // 暂停轮播
  const stop = () => {
    clearTimeout(timer);
    timer = null;
  };

  loop();
  // 鼠标事件
  viewport.addEventListener('mouseenter', stop);
  viewport.addEventListener('mouseleave', loop);

  window.addEventListener('visibilitychange', function () {
    document.hidden ? stop() : loop();
  });

  window.addEventListener('beforeunload', stop);
};

export default SliderNative;
