import { fileUpload, fileMerge } from '@/api/Interface/home/upload';
import Promise from '@/components/Promise';

/**
 * 分片上传
 * @param {*} fileInfo 文件信息
 */
const sliceUpload = async function (fileInfo) {
  const { filename, fileChunks } = fileInfo;
  // 上传队列
  const updateList = fileChunks.map((file) => {
    let form = new FormData();
    form.append('filename', filename);
    form.append('hash', file.hash);
    form.append('chunk', file.chunk);
    return fileUpload(form);
  });
  await Promise.all(updateList);
  fileMerge({ filename });
};

export default sliceUpload;
