import {
  CircleGeometry,
  DoubleSide,
  Mesh,
  MeshBasicMaterial,
  RingGeometry,
} from 'three';

// 圆环网格对象组
const circleYs = [];

const spotCircle = function (spot) {
  // 圆
  const geometry = new CircleGeometry(0.5, 200);
  const material = new MeshBasicMaterial({
    color: 0xff0000,
    side: DoubleSide,
  });
  const circle = new Mesh(geometry, material);
  // 绘制地图时 y轴取反 这里同步
  circle.position.set(spot[0], -spot[1], spot[2] + 0.1);

  // 圆环
  const geometry2 = new RingGeometry(0.5, 0.7, 50);
  // transparent 设置 true 开启透明
  const material2 = new MeshBasicMaterial({
    color: 0xff0000,
    side: DoubleSide,
    transparent: true,
  });
  const circleY = new Mesh(geometry2, material2);
  // 绘制地图时 y轴取反 这里同步
  circleY.position.set(spot[0], -spot[1], spot[2] + 0.1);

  return [circle, circleY];
};

export { spotCircle };
