import { AdditiveBlending, Color, FrontSide, ShaderMaterial } from 'three';
import { mainColor } from './GlobalConfig';

// 地球幽灵特效
const GlowMaterial = (
  color = mainColor,
  scale = -1.0,
  bias = 1.0,
  power = 4.0,
) => {
  const material = new ShaderMaterial({
    uniforms: {
      s: { type: 'f', value: scale },
      b: { type: 'f', value: bias },
      p: { type: 'f', value: power },
      glowColor: { type: 'c', value: new Color(color) },
    },
    vertexShader: `
    varying vec3 vNormal;
    varying vec3 vPositionNormal;
    void main() 
    {
      vNormal = normalize( normalMatrix * normal ); // 转换到视图空间
      vPositionNormal = normalize(( modelViewMatrix * vec4(position, 1.0) ).xyz);
      gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );
    }
    `,
    fragmentShader: `
    uniform vec3 glowColor;
    uniform float b;
    uniform float p;
    uniform float s;
    varying vec3 vNormal;
    varying vec3 vPositionNormal;
    void main() 
    {
      float a = pow( b + s * abs(dot(vNormal, vPositionNormal)), p );
      gl_FragColor = vec4( glowColor, a );
    }
    `,
    side: FrontSide,
    blending: AdditiveBlending,
    transparent: true,
  });
  return material;
};

export default GlowMaterial;
