import {
  BufferAttribute,
  BufferGeometry,
  Line,
  LineBasicMaterial,
  LineLoop,
  LineSegments,
  Points,
  PointsMaterial,
} from 'three';
import { randomHexColor } from '@/utils/common';

// 缓冲类型几何体
const geometry = new BufferGeometry();

const vertices = new Float32Array([
  0,
  0,
  0, //顶点1坐标
  50,
  0,
  0, //顶点2坐标
  0,
  100,
  0, //顶点3坐标
  0,
  0,
  10, //顶点4坐标
  0,
  0,
  100, //顶点5坐标
  50,
  0,
  10, //顶点6坐标
]);

const attribute = new BufferAttribute(vertices, 3);
geometry.attributes.position = attribute;

// 点模型
const pointMaterial = new PointsMaterial({
  color: randomHexColor(),
  size: 10.0, //点对象像素尺寸
});

const points = new Points(geometry, pointMaterial);

// 线模型
const lineMaterial = new LineBasicMaterial({
  color: randomHexColor(),
});
// const lines = new Line(geometry, lineMaterial);
const lines = new LineLoop(geometry, lineMaterial);
// const lines = new LineSegments(geometry, lineMaterial);

export { points, lines };
