import React, { useEffect, useRef, useState } from 'react';
import sea from './assets/sea.jpg';
import './style.less';

const flipList = [
  'JavaScript',
  '工程化',
  '网格',
  'HTML+CSS',
  'NodeJS',
  '移动端',
  '框架',
];
let domList = [];

const UserAction = () => {
  const [loading, setLoading] = useState(false);

  /**
   *
   * @param {*} videoFile 视频文件
   * @param {*} time 截取视频的时刻
   */
  const captrueFrame = (videoFile, time = 0) => {
    return new Promise((resolve) => {
      const video = document.createElement('video');
      video.muted = true; // 静音播放（浏览器可自动播放）
      video.autoplay = true;
      video.currentTime = time;
      video.src = URL.createObjectURL(videoFile); // 将文件信息转换为视频地址
      // 视频可播放
      video.oncanplay = () => {
        const cvs = document.createElement('canvas');
        cvs.width = video.videoWidth;
        cvs.height = video.videoHeight;
        const ctx = cvs.getContext('2d');
        ctx.drawImage(video, 0, 0, cvs.width, cvs.height);
        cvs.toBlob((blob) => {
          const url = URL.createObjectURL(blob);
          resolve({
            url,
            blob,
          });
        });
      };
    });
  };

  const previewImage = (url) => {
    const img = document.createElement('img');
    img.src = url;
    img.classList.add = 'previewList';
    document.querySelector('.preview').append(img);
  };
  // 上传
  const upload = async (e) => {
    try {
      setLoading(true);
      const [file] = e.target.files;
      if (!file) return;
      // 删除已有的帧图片
      if (document.querySelectorAll('.previewList').length) {
        const nodeList = document.querySelectorAll('.previewList');
        for (const img of nodeList) {
          document.querySelector('.preview').removeChild(img);
        }
      }
      for (let i = 0; i <= 9; i += 1) {
        // const resp = await captrueFrame(file, i);
        // previewImage(resp?.url);
        // 同时出现
        captrueFrame(file, i).then((result) => {
          previewImage(result?.url);
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  const [percent, setPercent] = useState(0);

  const innerGrap = useRef('');
  const MouseDown = (event) => {
    if (!(innerGrap && innerGrap.current)) return;
    const shiftX =
      event.clientX - innerGrap.current.getBoundingClientRect().left;
    function onMouseMove(event) {
      const slider = document.querySelector('.process');
      const { left, width } = slider.getBoundingClientRect();
      let distance = event.clientX - shiftX - left;
      if (distance < 0) distance = 0;
      let rightEdge = width - innerGrap.current.offsetWidth;
      if (distance > rightEdge) distance = rightEdge;
      const percent = +((distance / rightEdge) * 100).toFixed(2);
      innerGrap.current.style.left = percent + '%';
      setPercent(percent);
    }

    function onMouseUp() {
      document.removeEventListener('mouseup', onMouseUp);
      document.removeEventListener('mousemove', onMouseMove);
    }

    document.addEventListener('mousemove', onMouseMove);
    document.addEventListener('mouseup', onMouseUp);
  };

  useEffect(() => {
    let styleTag = document.querySelector('#styleTag');
    if (!styleTag) styleTag = document.createElement('style');
    styleTag.id = 'styleTag';
    let warning = '#fcd713',
      primary = '#2ef92e';
    styleTag.innerHTML = `
		.wave::before { top: ${
      100 - percent
    }%;}.wave::after { content: '${percent}%';color:${
      percent >= 80 ? warning : primary
    }}.process::before{width: ${percent}%}`;
    document.head.appendChild(styleTag);

    const innerGrap = document.querySelector('.innerGrap');
    innerGrap.style.transform = `translate(${
      percent >= 90 ? '-90%' : '-10%'
    }, -50%)`;
  }, [percent]);

  // 卡片旋转相关
  const card = useRef();
  const yRange = [-10, 10];
  const xRange = [-10, 10];
  // 获取旋转的角度
  const getRoateDeg = (range, value, length) =>
    (value / length) * (range[1] - range[0]) + range[0];
  const doCardMouseMove = (e) => {
    if (!(card && card?.current)) return;
    const { clientX, clientY } = e;

    const { width } = document
      .querySelector('.navigator')
      .getBoundingClientRect();

    const parentNode = card.current.parentNode;
    const { top } = parentNode.getBoundingClientRect();
    const { offsetWidth, offsetHeight, offsetLeft, offsetTop } = card?.current;
    const rx = getRoateDeg(xRange, clientY - top - offsetTop, offsetHeight);
    const ry = -getRoateDeg(yRange, clientX - width - offsetLeft, offsetWidth);
    card?.current.style.setProperty('--rx', rx + 'deg');
    card?.current.style.setProperty('--ry', ry + 'deg');
  };
  const MouseLeave = () => {
    card?.current.style.setProperty('--rx', 0 + 'deg');
    card?.current.style.setProperty('--ry', 0 + 'deg');
  };

  const HMouseEnter = (e) => {
    e.stopPropagation();
    const dom = document.querySelector('.list');
    dom.style.height = 'auto';
    dom.style.display = 'block';
    const { height } = dom.getBoundingClientRect();
    dom.style.height = 0;
    dom.offsetHeight;
    dom.style.height = height + 'px';
    dom.style.opacity = 1;
  };

  const HMouseLeave = () => {
    const dom = document.querySelector('.list');
    dom.style.height = 0;
    dom.style.opacity = 0;
    setTimeout(() => {
      dom.style.display = 'none';
    }, 400);
  };

  // 获取指定元素的位置信息
  const getPosition = (dom) => {
    // console.log(dom);
    const { top } = dom.getBoundingClientRect();
    return top;
  };
  let start = 0;
  useEffect(() => {
    start = getPosition(document.querySelector('.listItem:first-child'));
  }, []);

  const doSort = () => {
    const parentDom = document.querySelector('.wrapUl');
    const firstDom = document.querySelector('.listItem:first-child');
    parentDom.insertBefore(firstDom, null);
    const last = getPosition(firstDom);
    const invert = start - last;
    firstDom.style.transform = `translateY(${invert}px)`;

    raf(() => {
      firstDom.style.transition = 'transform 1s';
      firstDom.style.removeProperty('transform');
      start = getPosition(document.querySelector('.listItem:first-child'));
      console.log(start);
    });
  };

  const raf = (callback) => {
    requestAnimationFrame(() => {
      requestAnimationFrame(callback);
    });
  };

  const doTramsform = (dom, color = '#000', size = 1, deg = '90deg') => {
    if (!dom) return;
    dom.style.background = color;
    dom.style.transform = `scale(${size}) rotate(${deg})`;
  };

  const honeyHover = (e) => {
    const { index } = e.target.dataset;
    const [row, col] = index.split('+');
    const beeList = Array.from(document.querySelectorAll('.itemHoney'));
    const currentDom = beeList[+row * beeCol + +col - 1];

    doTramsform(currentDom, 'green', 1.3);
    domList = [];

    if (row > 0) {
      const top = beeList[(+row - 1) * beeCol + +col - 1];
      domList.push(top);
    }

    if (row > 0 && +row % 2 !== 0) {
      const topLeft = beeList[(+row - 1) * beeCol + +col - 1 - 1];
      domList.push(topLeft);
    }

    if (row > 0 && +row % 2 == 0) {
      const topRight = beeList[(+row - 1) * beeCol + +col];
      domList.push(topRight);
    }

    if (row < beeRow - 1) {
      const bottom = beeList[(+row + 1) * beeCol + +col - 1];
      domList.push(bottom);
    }

    if (row < beeRow - 1 && +row % 2 !== 0) {
      const bottomLeft = beeList[(+row + 1) * beeCol + +col - 1 - 1];
      domList.push(bottomLeft);
    }

    if (row < beeRow - 1 && +row % 2 == 0) {
      const bottomRight = beeList[(+row + 1) * beeCol + +col];
      domList.push(bottomRight);
    }

    if (col > 0) {
      const left = beeList[+row * beeCol + +col - 1 - 1];
      domList.push(left);
    }

    if (col < beeCol - 1) {
      const right = beeList[+row * beeCol + +col];
      domList.push(right);
    }
    domList.forEach((dom) => doTramsform(dom, 'red', 0.7));
    domList.push(currentDom);
  };

  const honeyLeave = () => {
    domList.forEach((dom) => {
      if (!dom) return;
      dom.offsetHeight;
      dom.style.background = '#000';
      dom.style.transform = `scale(${1}) rotate(90deg)`;
    });
  };

  const beeRow = 8,
    beeCol = 20;
  return (
    <div className="userAction">
      <>
        <a onChange={upload} className="upload">
          <input type="file" />
          {loading ? '文件上传中...' : '点击上传文件获取获取视频帧信息'}
        </a>
        <div className="preview"></div>
      </>
      {/* 波浪 */}
      <div className="box" />
      {/* 圆形进度条 */}
      <>
        <div className="wave" />
        <div className="process">
          <div
            className="innerGrap"
            ref={innerGrap}
            onDragStart={() => false}
            onMouseDown={MouseDown}
          />
        </div>
      </>

      {/* 鼠标hover效果 */}
      <>
        <div
          className="mousehover"
          onMouseEnter={HMouseEnter}
          onMouseLeave={HMouseLeave}
        >
          鼠标hover
          <div className="list">
            2023年，两江新区明确新能源智能网联汽车、新一代电子信息、生命健康等8条重点产业链，组建8支产业链融合招商团队。同时根据产业图谱招商，加快形成集聚效应；瞄准产业前沿招商，聚焦卫星互联网、工业软件等新兴产业，抓好高成长性、专精特新企业招商，引进一批尖端项目“首试”“首产”。
          </div>
        </div>
      </>

      {/* Flip */}
      <div className="flip">
        <span className="btn" onClick={doSort}>
          改变第一个元素位置
        </span>
        <ul className="wrapUl">
          {flipList.map((text, index) => (
            <li className="listItem" key={index}>
              {text}
            </li>
          ))}
        </ul>
      </div>

      {/* 卡片旋转样式 */}
      <div className="cardArea">
        <img
          src={sea}
          className="card"
          onMouseLeave={MouseLeave}
          ref={card}
          onMouseMove={doCardMouseMove}
        />
        {/* 混入效果 */}
        <div className="textWrap">
          <div className="text" datacontent="育人为渡，经久如一">
            育人为渡，经久如一
          </div>
        </div>
      </div>

      {/* 蜂窝布局 */}
      <div className="honeycomb">
        {new Array(beeRow).fill(1).map((item, i) => (
          <div className="line" key={i}>
            {new Array(beeCol).fill(1).map((v, index) => (
              <div
                key={index}
                data-index={`${i}+${index + 1}`}
                className="itemHoney"
                onMouseEnter={honeyHover}
                onMouseLeave={honeyLeave}
              />
            ))}
          </div>
        ))}
      </div>
    </div>
  );
};

export default UserAction;
