import React, { useEffect, useRef } from 'react';
import './style.less';
import * as echarts from 'echarts';

let myChart;
const Monitor = () => {
  const monitorRef = useRef();

  const rb = new ResizeObserver((entries) => {
    for (const item of entries) {
      const { contentRect } = item;
      console.log(contentRect);
      myChart.resize();
    }
  });

  const setMonitor = () => {
    rb.observe(monitorRef?.current);
  };

  // 初始化
  const initDraw = () => {
    myChart = echarts.init(monitorRef?.current);
    const option = {
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
      },
      yAxis: {
        type: 'value',
      },
      series: [
        {
          data: [820, 932, 901, 934, 1290, 1330, 1320],
          type: 'line',
        },
      ],
    };
    myChart.setOption(option);
  };

  useEffect(() => {
    if (monitorRef && monitorRef?.current) {
      setMonitor();
      initDraw();
    }
    return () => {
      rb.unobserve(monitorRef);
    };
  }, [monitorRef]);

  return (
    <>
      <div className="monitorWrap">
        <div className="monitorGraph" ref={monitorRef} />
      </div>
    </>
  );
};

export default Monitor;
