import {
  BufferAttribute,
  BufferGeometry,
  VertexColors,
  CatmullRomCurve3,
  Color,
  DoubleSide,
  LineBasicMaterial,
  Vector3,
  Line,
  QuadraticBezierCurve3,
  Object3D,
  Group,
} from 'three';
import { lineColor, radius } from './GlobalConfig';
import InitFlyLine from './InitFlyLine';
import { createPosition } from './tools';
import * as d3 from 'd3';
import { flyArc } from '../../../../../../Vector/threejs/components/3dEarth/Utils/arc';

// 球体半径 + 0.3
const flylineRadius = radius + 0.3;

const earthAddFlyLine = (earth, flyLineData = [], flyManager = null) => {
  if (!flyManager) flyManager = new InitFlyLine({});
  flyLineData.forEach((data) => {
    // 随机时间间隔添加连线，避免效果死板。
    randomAddFlyLine(earth, flyManager, data);
  });
  return flyManager;
};

const randomAddFlyLine = (earth, flyManager, data) => {
  setTimeout(() => {
    addFlyLine(earth, flyManager, data);
  }, Math.random() * 5000);
};

/**
 * 墨卡托投影
 */
const projection = d3.geoMercator().center([0, 0]).translate([0, 0]);

/**
 * 添加飞行线
 * @param {*} earth
 * @param {*} flyManager
 * @param {*} data
 */
const addFlyLine = (earth, flyManager, data) => {
  const [fromCity, toCity] = data?.line || [];
  const [startx, starty] = fromCity.position;
  const [endx, endy] = toCity.position;
  const arcline = flyArc(flylineRadius, startx, starty, endx, endy, {
    color: 0xf3ae76, // 飞线的颜色
    flyLineColor: 0xff7714, // 飞行线的颜色
    speed: 0.005, // 拖尾飞线的速度
  });
  earth.add(arcline);
  earth.userData['flyLineArray'].push(arcline.userData['flyLine']);
};

const addFlyLine1 = (earth, flyManager, data) => {
  const [fromCity, toCity] = data?.line || [];
  // 经纬度转为球体空间坐标
  const fromXyz = createPosition(fromCity.position, flylineRadius);
  const toXyz = createPosition(toCity.position, flylineRadius);
  //根据城市之间距离远近，取不同个数个点
  // Math.sqrt() 方法返回给定数的平方根。 Math.sqrt(9) = 3
  const distanceDivRadius =
    Math.sqrt(
      Math.pow(fromXyz.x - toXyz.x, 2) +
        Math.pow(fromXyz.y - toXyz.y, 2) +
        Math.pow(fromXyz.z - toXyz.z, 2),
    ) / flylineRadius;
  const partCount = 3 + Math.ceil(distanceDivRadius * 3); // 2个位置间设置点数
  // 将这些点拟合成一条曲线（这里没有使用贝赛尔曲线，因为拟合出来的点要在地球周围，不能穿过地球）
  const points = getSplinePoints([fromXyz, toXyz]);
  const curve = new CatmullRomCurve3(points, false); // 绘制3维贝赛尔曲线

  const pointCount = Math.ceil(500 * partCount); // 再次扩大、增加点的数量
  const allPoints = curve.getPoints(pointCount); // 取出组成曲线上的所有点
  // 制作飞线动画
  const flyMesh = flyManager.addFly({
    curve: allPoints, //飞线飞线其实是N个点构成的
    color: lineColor, //点的颜色
    width: 0.12, //点的半径
    haloWidth: 0.4,
    length: Math.ceil((allPoints.length * 3) / 3), //飞线的长度（点的个数）
    speed: partCount + 10, //飞线的速度
    repeat: Infinity, //循环次数
    data, // 飞线数据
  });

  const lineGeometry = new BufferGeometry();
  // 获取曲线上的50个点
  const pointLoop = curve.getPoints(50);
  const positions = [];
  const colors = [];
  const color = new Color();

  // 给每个顶点设置演示 实现渐变
  for (var j = 0; j < pointLoop.length; j++) {
    color.setHSL(0.81666 + j, 0.88, 0.715 + j * 0.25); // 粉色
    colors.push(color.r, color.g, color.b);
    positions.push(pointLoop[j].x, pointLoop[j].y, pointLoop[j].z);
  }
  // 放入顶点 和 设置顶点颜色
  lineGeometry.attributes.position = new BufferAttribute(
    new Float32Array(positions),
    3,
    true,
  );

  lineGeometry.attributes.color = new BufferAttribute(
    new Float32Array(colors),
    3,
    true,
  );

  const material = new LineBasicMaterial({
    vertexColors: VertexColors,
    side: DoubleSide,
    color: 0xf3ae76,
  });

  const line = new Line(lineGeometry, material);

  // 添加飞行线
  earth?.add?.(line);
  earth?.add?.(flyMesh);
};

/**
 * 两点之间分隔点
 * @param {*} positions
 */
const getSplinePoints = (positions) => {
  const [start, end] = positions;
  const points = [];
  points[0] = new Vector3(start.x, start.y, start.z); // 起点位置
  points[8] = new Vector3(end.x, end.y, end.z); // 终点

  const getPoint = (point1, point2) => {
    // 获取当前点到圆心的距离
    const temp1 = getUnitVector(point1);
    const temp2 = getUnitVector(point2);
    // 求出2点的空间中心点的位置坐标
    const tempMiddle = new Vector3(
      0.5 * (temp1.x + temp2.x),
      0.5 * (temp1.y + temp2.y),
      0.5 * (temp1.z + temp2.z),
    );
    // 空间中心点到圆心的距离
    const tempMiddleLength = Math.sqrt(
      tempMiddle.x ** 2 + tempMiddle.y ** 2 + tempMiddle.z ** 2,
    );
    // 球半径 / 一个点到球心的距离 = 该点在球体上的归一化坐标或称球面坐标
    // 这个归一化坐标可以用来表示：球体表面上的点相对于球心的相对位置，而不受球的具体尺寸影响。
    const rate = flylineRadius / tempMiddleLength;
    // 求取中心点的空间坐标位置
    return new Vector3(
      rate * tempMiddle.x,
      rate * tempMiddle.y,
      rate * tempMiddle.z,
    );
  };
  //以中点的方式
  points[4] = getPoint(points[0], points[8]);
  points[2] = getPoint(points[0], points[4]);
  points[1] = getPoint(points[0], points[2]);
  points[3] = getPoint(points[2], points[4]);
  points[6] = getPoint(points[4], points[8]);
  points[5] = getPoint(points[2], points[8]);
  points[7] = getPoint(points[6], points[8]);
  return dealPoints(points);
};

// 球体上的点的坐标是 (x, y, z)，那么计算该点到原点的距离（模）可以按照以下步骤进行：
// 将 x、y、z 坐标的平方相加：x^2 + y^2 + z^2
// 对上述和取平方根：sqrt(x^2 + y^2 + z^2)
// 结果就是该点到原点的距离。

const getUnitVector = (vector) => {
  const distance = Math.sqrt(vector.x ** 2 + vector.y ** 2 + vector.z ** 2);
  return new Vector3(
    vector.x / distance,
    vector.y / distance,
    vector.z / distance,
  );
};

const dealPoints = (points) => {
  // console.log(points);
  const start = points[0];
  const end = points.at(-1);
  // 开始结束点相对于飞行半径的比例
  const chordLengthRate =
    Math.sqrt(
      (start.x - end.x) ** 2 + (start.y - end.y) ** 2 + (start.z - end.z) ** 2,
    ) / flylineRadius;
  const middleNum = Math.floor(points.length / 2);

  // 返回每个飞行线点的空间坐标
  return points.map((point, index) => {
    const a = Math.abs(middleNum - index); // 求绝对值
    const rate =
      ((+`1.${Math.log(middleNum + 1 - a) // 自然对数
        .toString()
        .replace('.', '')}` -
        1) *
        chordLengthRate) /
        2 +
      1;
    return new Vector3(rate * point.x, rate * point.y, rate * point.z);
  });
};
export { earthAddFlyLine };
