import React, { useEffect } from 'react';
import './style.less';
import plane from './assets/plane.svg';

const MouseMove = () => {
  useEffect(() => {
    const pointer = document.querySelector('.pointer');
    const parentNode = document.querySelector('.textSkill');
    window.onmousemove = function (e) {
      let rad = 0;
      if (Math.abs(e.movementX) + Math.abs(e.movementY) > 3)
        rad = Math.atan2(e.movementX, -e.movementY);
      pointer.style.display = 'block';
      pointer.style.transform = `translate(${
        e.clientX - parentNode.offsetLeft
      }px,${e.clientY - 60}px) rotate(${rad}rad)`;
    };
  }, []);
  return (
    <div className="MouseMove">
      <img className="pointer" src={plane} />
    </div>
  );
};

export default MouseMove;
