import React, { useEffect, useRef } from 'react';
import { startApp } from '../..';
import DragMenu from '@/components/DragMenu';
import './style.less';

const Qiankun = () => {
  // 主应用中的某个地方
  window.addEventListener('message', (event) => {
    // 判断是否来自子应用的消息
    if (event.data && event.data.type === 'SUB_APP_DOM_INFO') {
      const info = event.data?.payload;
      console.log(info);
    }
  });

  const microApp = useRef(null);

  useEffect(() => {
    if (microApp && microApp.current) {
      startApp();
    }
  }, [microApp]);

  return (
    <div className="microContainer">
      <div id="microApp" ref={microApp} />
      <DragMenu />
    </div>
  );
};

export default Qiankun;
